<template>
  <div class="user">
    <div class="container">
      <div class="row" style="margin-top:80px;margin-bottom: 100px">
        <div class="col-lg-6 col-md-6 col-sm-6 col-xs-6" style="text-align: center">
          <div>
            <span><button @click="onConsult">咨询服务</button></span>
            <span><button @click="onRegister">纠纷登记</button></span>
          </div>
          <br>
          <div>
            <span><button @click="onProgress()">案件进展</button></span>
            <span><button @click="onQuickQ">快捷查询</button></span>
          </div>
        </div>

        <div class="col-lg-6 col-md-6 col-sm-6 col-xs-6">
          <textarea  rows="10" style="background: none;width: 90%;border: none">
            平台简介平台简介平台简介平台简介平台简介平台简介平台简介平台简介平台简介平台简介平台简介平台简介平台简介平台简介平台简介平台简介平台简介
          </textarea>
        </div>

      </div>
      <div class="row">
        <div class="col-lg-8 col-md-8 col-sm-8 col-xs-8" style="text-align: center;padding-left: 30px">
          <textarea  rows="10" style="width: 90%;background: none;width: 90%">解纷流程</textarea>
          <!--<img src="../assets/picture/process.png" alt="解纷流程" width=700 height="200">-->
        </div>
        <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
          <img src="../assets/picture/robot.png" alt="智能咨询" width="150" height="180" style="float: right;cursor: pointer" @click="onConsult">
        </div>
      </div>

    </div>
  </div>
</template>

<script>
  import LoginForm from '../components/loginForm'
  import RegisterForm from '../components/registerForm'
  export default {
    data(){
      return{
        role:sessionStorage.getItem("role"),
        phone:sessionStorage.getItem("phone"),
        id:sessionStorage.getItem("id"),
        isLogin:sessionStorage.getItem("isLogin")
      }
    },
    mounted(){
    },
    components: {
      LoginForm,RegisterForm
    },
    methods: {
      onConsult() {
        let route=this.$router.resolve({path: '/consult'});
        window.open(route.href,'_blank')
      },
      onRegister() {
        let route=this.$router.resolve({path: '/RegConflictPage'});
        window.open(route.href,'_blank')
      },
      onProgress() {
        let route=this.$router.resolve({path: '/userCenter/userProcess'});
        window.open(route.href,'_blank')
        // this.$router.push({path: '/userCenter/userProcess'});
      },
      onQuickQ() {
        window.open('http://218.94.1.180:9090/First.jsp','_blank')
      },
    }
  }
</script>

<style scoped>

  .container {
    margin-top: 20px;
    width: 90%;
    margin-left: auto ;
    margin-right: auto;
    /*border-radius: 15px;*/
    /*height: 700px;*/
    /*border: 5px ridge;*/
  }

  .row button {
    background: #2e6da4;
    font-family: 宋体;
    font-size: 40px;
    color: #ffffff;
    margin-right: 20px;
  }


</style>



