<template>
  <div id="authorityAllocate">
    <view-page >
      <!--&lt;!&ndash; 左按钮区 &ndash;&gt;-->
      <!--<template slot="left-field">-->
      <!--<button class="btn btn-primary" @click="addMeditor" >添加调解员</button>-->
      <!--</template>-->
      <!-- 搜索框 -->
      <!--<template slot="search-field">-->
        <!--<input v-model="searchStr"placeholder="请输入调解员姓名搜索" style="height: 28px">-->
      <!--</template>-->
      <!-- 过滤条件区 -->
      <template slot="filter-field">
        <!-- 搜索框 -->
        <input v-model="searchStr"placeholder="请输入搜索内容" style="height: 28px">
        <span class="glyphicon glyphicon-zoom-in" aria-hidden="true"></span>
        <!-- 状态过滤框 -->
        <select v-model="filterType" placeholder="是否具有案件受理权限" @change="changeStatus" style="height: 28px">
          <option label="具有受理案件权限（全部）" value=""></option>
          <option v-for="(item,index) in statuses" :key="item" :label="item" :value="index"></option>
        </select>
        <!--调解员过滤框-->
        <select v-model="filterType2" placeholder="是否具有上报司法厅权限" @change="changeStatus" style="height: 28px">
          <option label="具有上报司法厅权限(全部)" value=""></option>
          <option v-for="(item,index) in statuses" :key="item" :label="item" :value="index"></option>
        </select>
      </template>
      <!-- 左按钮区 -->
      <template slot="right-field">
        <button class="btn btn-primary" @click="addMeditor" >添加调解员</button>
      </template>
      <!-- 表格区 -->
      <template slot="table">
        <div class="table-responsive">
          <table class="table  table-hover table-bordered">
            <thead class="tablehead">
            <tr>
              <th>姓名</th>
              <th>立案审批权</th>
              <th>上报司法厅</th>
            </tr>
            </thead>
            <tbody v-for="(item,index) in AuthorityNameList" class="tablebody">
            <tr>
              <td>{{item.name}}</td>
              <td>
                <input type="checkbox" v-model="item.authority1" @change="MyClick(index)">{{item.authority1}}
              </td>
              <td>
                <input type="checkbox" v-model="item.authority2" @change="MyClick(index)">{{item.authority2}}
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </template>
      <!--分页-->
      <template slot="pagination">
        共{{totalPages}}页
        <select name="" id="" v-model="currentPageSize" style="margin-left: 30px"@change="changeStatus">
          <!--<option label="2" ></option>-->
          <option v-for="item in PageSizes" :value="item" :label="item"></option>
        </select>条/页

        <button class="btn btn-info" @click="PreviousPage"  style="margin-left: 30px">上一页</button>
        <!--第{{currentPage}}页-->
        <button class="btn btn-info" @click="NextPage"  style="margin-right: 30px">下一页</button>

        前往第
        <input type="number" style="width:50px" v-model="currentPage"   min="1" @change="currentPageContent">页
      </template>
    </view-page>

    <!-- 弹出框 -->
    <div>
      <AddMediatorDialog :is-show="addDialogShow" title="添加调解员" @close="closeEditDialog" @submit="submit">
        <table class="table table-bordered">
          <thead>
          <tr>
            <th>调解员姓名</th>
            <th>身份证号</th>
            <th>手机号</th>
            <th>密码</th>
            <th>所在调解中心</th>
            <th>立案审批权</th>
            <th>上送司法厅权限</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item,index) in addMeditorList">
            <td ><input class="input0" v-model="item.mediatorName" ></td>
            <td ><input class="input0"v-model="item.idCard" ></td>
            <td ><input class="input0" v-model="item.phone" ></td>
            <td ><input class="input0" v-model="item.password" ></td>
            <td ><input class="input0" v-model="item.mediateCenter" ></td>
            <td width="110px">
              <input type="radio" v-model="item.authorityConfirm" value="true">true
              <input type="radio" v-model="item.authorityConfirm" value="false">false
            </td>
            <td width="110px">
              <input type="radio" v-model="item.authorityJudiciary" value="true">true
              <input type="radio" v-model="item.authorityJudiciary" value="false">false
            </td>
          </tr>
          </tbody>
        </table>
        <div style="text-align: center">
          <button class="btn btn-primary" @click="AddLine()">添加</button>
          <button class="btn btn-primary" @click="DeleteLine()">删除</button>
        </div>
      </AddMediatorDialog>
    </div>
  </div>
</template>

<script>
  import ViewPage from '../../components/ViewPage'
  import Dialog from '../../components/Dialog'
  import Vue from 'vue'
  import axios from 'axios'
  export default {
    name: "authority-allocate",
    components:{
      ViewPage,
      AddMediatorDialog: Dialog,
    },
    data(){
      return{
        AuthorityNameList:[],
        statuses: ['不具有', '具有',],
        filterType: '',
        filterType2: '',
        searchStr: '',
        currentPage: 1,
        currentPageSize: 10,
        PageSizes: [2, 5, 10],
        totalPages:1,
        addDialogShow:false,
        num:1,
        addMeditorList:[
          {
            mediatorName:'',
            phone:'',
            password:'',
            idCard:'',
            mediateCenter:'',
            authorityConfirm:'',
            authorityJudiciary:'',
          }
        ],
      }
    },
    mounted() {
      //管理员 获取所有调解员的授权信息
      this.$axios.post('https://www.wangj1106.top/DisputeWeb/manager/getNameofAuthorityList',{
        id:sessionStorage.getItem("id"),
        page:this.currentPage,
        size:this.currentPageSize,
      })
        .then((res) => {
          this.AuthorityNameList = res.data.data.mediatorAuthorityFormList,
            this.totalPages=res.data.data.totalPages
        })
        .catch((error) => {
          console.log(error)
        });
    },
    methods: {
      addMeditor(){
        this.addDialogShow=true;
      },
      closeEditDialog() {
        this.addDialogShow = false
      },
      submit() {
        for(let i=0;i<=this.addMeditorList.length-1;i++){
          if(this.addMeditorList[i].authorityConfirm==true)
            this.addMeditorList[i].authorityConfirm=1
          else
            this.addMeditorList[i].authorityConfirm=0
          if(this.addMeditorList[i].authorityJudiciary==true)
            this.addMeditorList[i].authorityJudiciary=1
          else
            this.addMeditorList[i].authorityJudiciary=0
        }
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/user/registerMediator',
          data:{addMeditorList:this.addMeditorList}
        })
        this.closeEditDialog()
      },
      MyClick(index){
        let param={
          mediatorId:this.AuthorityNameList[index].id,
          authority1:this.AuthorityNameList[index].authority1,
          authority2:this.AuthorityNameList[index].authority2,
        }
        this.$axios({
          method:'post',
          url:'http://223.3.93.238:8080/DisputeWeb/manager/ChangeAuthorityNameList',
          data:param
        })
      },
      AddLine() {
        let temp = {
          name: '',
          phone: '',
          password:'',
        }
        this.addMeditorList.push(temp)
      },
      DeleteLine() {
        this.addMeditorList.pop()
      },
      refresh() {
        location.reload()
      },
      // // 排序
      // sortChange(column) {
      //   this.sortProp = column.prop;
      //   this.sortOrder = column.order
      // },
      //分页
      PreviousPage(){
        if(this.currentPage>1) {
          this.currentPage = this.currentPage - 1;
          this.currentPageContent();
        }
      },
      NextPage(){
        if(this.currentPage<this.totalPages){
          this.currentPage=this.currentPage+1;
          this.currentPageContent();
        }
      },
      changeStatus(){
        this.currentPage=1;
        this.currentPageContent();
      },
      currentPageContent(){
        if(this.currentPage<=0)
        {
          alert("页数必须大于0")
          this.currentPage=1;
        }
        this.$axios.post('http://223.3.93.238:8080/DisputeWeb/manager/getNameofAuthorityList',{
          id:sessionStorage.getItem("id"),
          page:this.currentPage,
          size:this.currentPageSize,
          filterType:this.filterType,
          filterType2:this.filterType2,
        })
          .then((res) => {
            this.AuthorityNameList = res.data.data.mediatorAuthorityFormList,
              this.totalPages=res.data.data.totalPages
          })
          .catch((error) => {
            console.log(error)
          });
      }
    },
  }
</script>

<style scoped>
  #authorityAllocate {
    padding-left: 8px;
    padding-right: 8px;
  }
  .table{
    margin-top: 10px;
  }
  .tablehead{
    font-size: 18px;
  }
  .tablebody{
    font-size: 16px;
  }
  button{
    border-radius: 5px;
  }
  table .input0{
    width: 100%;
    border: none;
  }
</style>



