<template>
    <div id="content">
      <view-page>
        <template slot="filter-field">
          <!-- 状态过滤框 -->
          <label for="" >案件选择：</label>
          <select v-model="filterStatus" placeholder="选择案件状态(全部)" style="height: 28px" @change="changeStatus">
            <option label="选择案件状态(全部)" value="" ></option>
            <option v-for="(status,index) in statuses" :key="status" :label="status" :value="index"></option>
          </select>
        </template>
        <template slot="table">
          <div class="table-responsive">
            <table class="table table-bordered table-hover">
              <thead  class="tablehead">
              <tr>
                <th>案件名称</th>
                <th>专家状态</th>
                <th>操作</th>
                <th>查看详情</th>
              </tr>
              </thead>
              <tbody v-for="item in data" class="tablebody">
              <tr >
                <td>{{item.name}}</td>
                <td :class=caseColor[item.caseStatus]>{{item.caseStatus}}</td>
                <td>
                  <template>
                    <div v-if="item.caseStatus=='等待分配'">
                      <button class="btn btn-success" @click="expertAllocate(item.caseId)">分配专家</button>
                    </div>
                  </template>
                </td>
                <td>
                  <button class="btn btn-primary" @click="ForCaseDetail(item)">查看详情</button>
                </td>
              </tr>
              </tbody>
            </table>
          </div>
        </template>
        <template slot="pagination">
          共{{caseTotalPages}}页
          <select  v-model="caseCurrentPageSize" style="margin-left: 30px" @change="CasechangePageSize">
            <option v-for="item in PageSizes" :value="item" :label="item"></option>
          </select>条/页

          <button class="btn btn-info" @click="CasePreviousPage" style="margin-left: 30px">上一页</button>
          <!--第{{currentPage}}页-->
          <button class="btn btn-info" @click="CaseNextPage"  style="margin-right: 30px">下一页</button>
          前往第
          <input type="number" style="width:50px" v-model="caseCurrentPage">页
        </template>
      </view-page>
      <expert-dialog :is-show="expertShow" title="另外分配" @close="closeDialogExpert" @submit="submitExpert">
        <div class="container" style="width: 100%;margin-top: 20px">
          <div class="row">
            <div class="col-md-2" v-for="item in expertList" style="height: 50px">
              <input type="radio" v-model="radio" :value="item.id">{{item.name}}
            </div>
          </div>
        </div>
        共{{totalPages}}页
        <select name="" id="" v-model="currentPageSize" style="margin-left: 30px" @change="changePageSize">
          <option v-for="item in PageSizes" :value="item" :label="item"></option>
        </select>条/页

        <button class="btn btn-info" @click="PreviousPage" style="margin-left: 30px">上一页</button>
        <button class="btn btn-info" @click="NextPage"  style="margin-right: 30px">下一页</button>
        前往第
        <input type="number" style="width:50px" v-model="currentPage">页
      </expert-dialog>
    </div>
</template>

<script>
  import ViewPage from '../components/ViewPage'
  import Dialog from '../components/Dialog'
  export default {
    name: "Allocatee",
    components: {
      ViewPage,
      ExpertDialog: Dialog
    },
    data(){
        return{
          CurrentCaseId:'',
          caseCurrentPage:1,
          caseCurrentPageSize:10,
          caseTotalPages:1,

          currentPage:1,
          currentPageSize:10,
          PageSizes:[10,25,50],
          totalPages:1,

          expertShow:false,
          expertList:[{name:'wkj',id:23}],
          caseColor:{尚未分配:'info', 等待分配:'warning', 已分配:'success'},
          statuses:['尚未分配','等待分配','已分配'],
          data:[
            {caseId:'',name:'cbdabcdc',caseStatus:'尚未分配'},
            {caseId:'',name:'cbdabcdc',caseStatus:'等待分配'},
            {caseId:'',name:'cbdabcdc',caseStatus:'已分配'},
            {caseId:'',name:'cbdabcdc',caseStatus:'尚未分配'},
            {caseId:'',name:'cbdabcdc',caseStatus:'等待分配'},
            {caseId:'',name:'cbdabcdc',caseStatus:'已分配'},
            {caseId:'',name:'cbdabcdc',caseStatus:'尚未分配'},
            {caseId:'',name:'cbdabcdc',caseStatus:'等待分配'},
            {caseId:'',name:'cbdabcdc',caseStatus:'已分配'},
            {caseId:'',name:'cbdabcdc',caseStatus:'尚未分配'},
            {caseId:'',name:'cbdabcdc',caseStatus:'等待分配'},
            {caseId:'',name:'cbdabcdc',caseStatus:'已分配'},
            ],
          radio:'',
          filterStatus:''
        }
    },
    methods:{
      //将选择的专家发送给后台
      submitExpert(){
        this.$axios.post('http://223.3.83.239:8080/DisputeWeb/manager/postExpertChosed',
          {
            // size:this.currentPageSize,
            // page:this.currentPage,
            caseId:this.CurrentCaseId,
            expert:this.radio,
          })
          .then((res) => {
          })
          .catch((error) => {
            console.log(error)
          });
        this.closeDialogExpert()
      },
      closeDialogExpert(){
        this.expertShow=false
      },
      ForCaseDetail(para) {
        let caseName = para.name;
        let caseId = para.caseId;
        let route = this.$router.resolve({
          path: '/CaseDetailPage',
          query: {
            prePath: this.$route.path,
            caseName: caseName,
            caseId: caseId
          }
        });
        window.open(route.href, '_blank');
      },
      expertAllocate(para){
        this.expertShow=true;
        this.CurrentCaseId=para
      },
      //专家列表分页
      PreviousPage(){
        if(this.currentPage>1) {
          this.currentPage = this.currentPage - 1;
          this.currentPageContent();
        }
      },
      NextPage(){
        if(this.currentPage<this.totalPages){
          this.currentPage=this.currentPage+1;
          this.currentPageContent();
        }
      },
      changePageSize(){
        this.currentPage=1;
        this.currentPageContent();
      },
      //显示当前页内容
      currentPageContent(){
        //请求专家库
        this.$axios.post('http://223.3.83.239:8080/DisputeWeb/manager/getExpertList',
          {caseId:this.tempCaseId,page:this.currentPage,size:this.currentPageSize})
          .then((res) => {
            this.MediatorList = res.data.data,
              this.totalPages=res.data.data.totalPages
          })
          .catch((error) => {
            console.log(error)
          });
      },

      changeStatus(){
        this.currentPage=1;
        this.CasecurrentPageContent();
      },

      //专家分配页面分页
      CasePreviousPage(){
        if(this.caseCurrentPage>1) {
          this.caseCurrentPage = this.caseCurrentPage - 1;
          this.CasecurrentPageContent();
        }
      },
      CaseNextPage(){
        if(this.caseCurrentPage<this.caseTotalPages){
          this.caseCurrentPage=this.caseCurrentPage+1;
          this.CasecurrentPageContent();
        }
      },
      CasechangePageSize(){
        this.caseCurrentPage=1;
        this.CasecurrentPageContent();
      },
      //显示当前页内容
      CasecurrentPageContent(){
        //请求案件列表
        this.$axios.post('http://223.3.83.239:8080/DisputeWeb/allocatee/getAllocateeCaseList',
          {
            size:this.caseCurrentPageSize,
            page:this.caseCurrentPage,
            filterStatus:this.filterStatus,
          })
          .then((res) => {
            //从后台返回的数据
            // this.data = res.data.data,
            // this.totalPages=res.data.data.totalPages
          })
          .catch((error) => {
            console.log(error)
          });

      },
    },
    mounted(){
      this.$axios({
        methods:'get',
        url:'http://223.3.83.239:8080/DisputeWeb/allocatee/getExpertList',
        data:{
          caseId:this.tempCaseId,
          size:this.currentPageSize,
          page:this.currentPage,
        }
      })
        .then(res=>{
          //取决于后端返回数据的格式
          // this.expertList=res.data();
        })
        .catch((error) => {
          console.log(error)
        });


    //  获取专家分配页面的数据
      this.$axios.post('http://223.3.83.239:8080/DisputeWeb/allocatee/getAllocateeCaseList',
        {
          size:this.caseCurrentPageSize,
          page:this.caseCurrentPage,
          filterStatus:this.filterStatus,
        })
        .then((res) => {
          //从后台返回的数据
          // this.data = res.data.data,
          // this.totalPages=res.data.data.totalPages
        })
        .catch((error) => {
          console.log(error)
        });

    }
  }
</script>

<style scoped>
  #content {
    margin-top: 10px;
    padding-left: 8px;
    padding-right: 8px;
  }
  .table{
    margin-top: 10px;
  }
  .tablehead{
    font-size: 18px;
  }
  .tablebody{
    font-size: 16px;
  }
  button{
    border-radius: 10px;
  }
  label{
    font-size: 15px;
    font-family: "Times New Roman";
  }
</style>



