<template>
  <div style="width: 100%;margin-left: auto;margin-right: auto;">
    <div style="text-align: center;margin-bottom: 10px;margin-top: 10px">
      <label>{{caseName}}</label>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2">
          <div class="daohang">
            <label style="margin-bottom: 30px">调解过程</label>
            <br>
            <ul class="list-unstyled">
              <li @click="toggle(0)" :class="{Cclass:flag[0]}" v-if="identiQualify==true">医疗损害鉴定</li>
              <li @click="toggle(1)" :class="{Cclass:flag[1]}">预约调解</li>
              <li @click="toggle(2)" :class="{Cclass:flag[2]}">正在调解</li>
            </ul>
          </div>
        </div>
        <div class="col-lg-10 col-md-10 col-sm-10 col-xs-10">
          <div class="content">
            <keep-alive>
              <p :is="currentView" :caseId="caseId" :currentStatus="currentStatus" :currentStageContent="currentStageContent"
                 :expert="expert"  :applicants="applicants" :respondents="respondents"
                 :identified="identified" :isSuspended="isSuspended" :expertAppoint="expertAppoint"
                 @next="nextStep" @previous="previousStep"></p>
            </keep-alive>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import identification from '../../../components/mediating/Identification'
  import arrangeMediation from '../../../components/mediating/ArrangeMediation'
  import toMediating from '../../../components/mediating/ToMediating'
  import Vue from 'vue'

  export default {
    data() {
      return {
        currentView: 'identification',
        caseName: this.$route.query.caseName,
        caseId: this.$route.query.caseId,
        flag: [false, false, false],

        stage: '',//当前阶段
        currentStatus:0,//当前步骤，代表医疗鉴定、预约调解、正在调解
        isSuspended:'',//挂起状态
        applicants:'',
        respondents:'',
        identiQualify:'',//是否具备鉴定资格
        identified: '',//是否做过鉴定
        expert: '', //  是否能预约专家
        expertAppoint:'',//是否预约过专家
        currentStageContent:{
          resultOfIdentify:{},//鉴定结果
          particopate: [],//参与对象：申请人、被申请人、专家
          particopateContact: {
            expertChoosed:{
              name:'',
              id:'',
              phone:'',
              email:'',
            },
            mediationPlace: '', //调解时间
            mediationTime: '' , //调解地点
            //上传的文件
            currentFiles: [
            ]
          }
        }
      }
    },
    components: {
      identification, arrangeMediation, toMediating
    },
    created() {
      this.$axios({
        method:'post',
        url:'https://www.wangj1106.top/DisputeWeb/mediator/getMediationStage',
        data:{caseId:this.caseId}
      })
        .then((res)=>{
          this.stage=res.data.data.stage;
          this.currentStatus=res.data.data.currentStatus;
          this.identiQualify=res.data.data.identiQualify;
          this.identified=res.data.data.identified;
          this.isSuspended=res.data.data.isSuspended;
          this.currentStageContent=res.data.data.currentStageContent;
          // if(res.data.data.resultOfIdentify!='')
          //   this.resultOfIdentify=JSON.parse(res.data.data.resultOfIdentify);
          this.expert=res.data.data.expert;
          this.expertAppoint=res.data.data.expertAppoint;
          this.applicants=res.data.data.applicants;
          this.respondents=res.data.data.respondents;
          if (this.currentStatus == 0) {
            this.currentView = 'identification';this.toggle(0);
          }
          else if (this.currentStatus == 1) {
            this.currentView = 'arrange-mediation';this.toggle(1);
          }
          else {
            this.currentView = 'to-mediating';this.toggle(2);
          }
          // this.
          console.log(this.currentStageContent.resultOfIdentify)
          console.log(this.currentStageContent.resultOfIdentify.text)
        })
    },
    methods: {
      toggle(e) {
        if (e == 0)
          this.currentView = 'identification'
        else if (e == 1)
          this.currentView = 'arrange-mediation'
        else
          this.currentView = 'to-mediating'
        let i = 0;
        for (; i < this.flag.length; i++) {
          if (i == e) {
            Vue.set(this.flag, e, 'true');
          }
          else {
            this.flag[i] = false;
          }
        }
      },
      nextStep(e){
        this.toggle(e+1)
      },
      previousStep(e){
        this.toggle(e-1)
      }
    }
  }
</script>

<style scoped>
  .content {
    margin-top: 10px;
    border-radius: 20px;
    height: 650px;
    padding-top: 30px;
    background-color:rgba(0,0,0,0.03);
    padding-left: 40px;
    padding-right: 40px;
  }
  .container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  .daohang {
    background: none;
    padding: 10px 15px;
    margin-bottom: 25px;
    text-align: center;
  }
  .daohang li {
    text-align: center;
    padding: 10px 15px;
    cursor: pointer;
    font-size: 16px;
    margin-bottom: 10px;
    /*font-weight: bold;*/
  }
  .daohang li.active,
  .daohang li:hover {
    background: #3a8dbe;
    color: #fff;
    border-radius: 15px;
  }
  .Cclass {
    color: #ffffff;
    background: #3a8dbe;
    border-radius: 10px;
  }
  label{
    font-size: 20px;
    color: #3a8dbe;
  }
</style>




