<template>
  <div>
    <label for="">相关案例推荐</label>
    <div class="info">
      <ul id="myTab" class="nav nav-tabs">
        <li class="active">
          <a href="#A" data-toggle="tab">裁判文书</a>
        </li>
        <li><a href="#B" data-toggle="tab">典型案例</a></li>
        <li><a href="#C" data-toggle="tab">纠纷调解</a></li>
      </ul>

      <div id="myTabContent" class="tab-content">
        <div class="tab-pane fade in active" id="A">
          <table class="table  table-bordered  table-hover" style="width: 100%">
            <thead>
            <tr>
              <th>登记时间</th>
              <th>案件名称</th>
              <th>简要情况</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in list1" style="border: none">
              <td >{{item.registerDate}}</td>
              <td >{{item.disputeName}}</td>
              <td @click="caseDetails(item.disputeName,'裁判文书')">{{item.abstract}}</td>
            </tr>
            </tbody>
          </table>
        </div>

        <div class="tab-pane fade" id="B">
          <table class="table  table-bordered  table-hover" style="width: 100%">
            <thead>
            <tr>
              <th>登记时间</th>
              <th>案件名称</th>
              <th>简要情况</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in list2" style="border: none">
              <td >{{item.registerDate}}</td>
              <td >{{item.disputeName}}</td>
              <td @click="caseDetails(item.disputeName,'典型案例')">{{item.abstract}}</td>
            </tr>
            </tbody>
          </table>
        </div>

        <div class="tab-pane fade" id="C">
          <table class="table  table-bordered  table-hover" style="width: 100%">
            <thead>
            <tr>
              <th>登记时间</th>
              <th>案件名称</th>
              <th>简要情况</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in list3" style="border: none">
              <td >{{item.registerDate}}</td>
              <td>{{item.disputeName}}</td>
              <td  @click="caseDetails(item.disputeName,'纠纷调解')">{{item.abstract}}</td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <div class="confirm">
      <button class="btn btn-primary" @click="PreviousStep">上一步</button>
      <button class="btn btn-primary"  @click="NextStep">下一步</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "RelatedCase",
    data() {
      return {
        list1:[{
          registerDate:'1',
          disputeName:'2',
          abstract:'3'
        }],
        list2:[
          {
            registerDate:'1',
            disputeName:'2',
            abstract:'3'
          }
        ],
        list3:[
          {
            registerDate:'1',
            disputeName:'2',
            abstract:'3'
          }
        ],
        role: ''
      }
    },
    methods: {
      PreviousStep(){
        this.$router.push({path: 'KeyWordPage'});
      },
      NextStep() {
        this.role = sessionStorage.getItem("role");
        if (this.role == "普通用户")
          this.$router.push({path: 'InfoOfModeratorPage'});
        else {
          this.$router.push({path: 'MederatorRegister'});
        }
      },
        caseDetails(para,kind){
          // this.$axios({
          //   method:'get',
          //   url:'https://www.wangj1106.top/search/caseDetails',
          //   data:{
          //     caseId:sessionStorage.getItem("caseId")}
          // })
          //   .then((res)=>{
          //     this.list1=res.data.典型案例;
          //     this.list2=res.data.裁判文书;
          //     this.list3=res.data.纠纷调解;
          //   })
          this.$router.push(
            {
              path: '/DisplayCase',
              query:{
                caseName:para,
                caseKind:kind
              }
            }
            )
        }
    },
    mounted(){
      this.$axios({
        method:'get',
        url:'https://www.wangj1106.top/search/similarCases',
        data:{
          caseId:sessionStorage.getItem("caseId")}
      })
        .then((res)=>{
          this.list1=res.data.典型案例;
          this.list2=res.data.裁判文书;
          this.list3=res.data.纠纷调解;
        })
    }
  }
</script>

<style scoped>
  .info {
    width: 100%;
  }
  label {
    margin: 8px auto;
    font-size: 20px;
    color: #3a8dbe;
  }
  #myTabContent{
    height:600px;
    overflow: scroll;
    overflow-x:hidden;
    background-color:rgba(0,0,0,0.02);
    border-left:1px solid rgba(195, 199, 193, 0.84);
    border-right:1px solid rgba(195, 199, 193, 0.84);
    border-bottom:1px solid rgba(195, 199, 193, 0.84);

  }
  .confirm {
    margin-top: 20px;
    text-align: center;
  }
  .confirm button {
    width: 80px;
    margin-left:30px;
    margin-right: 30px;
  }
  button {
    font-size: 16px;
    border-radius: 5px;
  }
</style>



