<template>
  <div>
    <div class="info">
      <ul>
        <router-link to="firstPage" tag="li" active-class="active">简要情况</router-link>
        <router-link to="secondPage" tag="li" active-class="active">基本信息|</router-link>
        <router-link to="thirdPage" tag="li" active-class="active">纠纷概况|</router-link>
        <router-link to="fourthPage" tag="li" active-class="active">医疗行为</router-link>
      </ul>

      <div class="container">
        <div class="row">
          <div class="col-md-3" style="                                                                                                                                                                                                                                                                                                                                                                    text-align: right">
            <label class="lable2">疾病名称</label>
          </div>
          <div class="col-md-5">
            <select class="section1" style="font-size: 18px;background-color: transparent;border-radius: 10px;">
              <option class="item" value="volvo"><label>请选择</label></option>
            </select>
          </div>
          <div class="col-md-2" style="text-align: left">
            <button type="button" class="btn btn-primary " data-toggle="modal" data-target="#myModal1">+</button>
          </div>
        </div>
        <br/>
        <br/>

        <div class="row">
          <div class="col-md-3" style=" text-align: right">
            <label class="lable2">损害等级</label>
          </div>
          <div class="col-md-5">
            <select class="section1" style="font-size: 18px;background-color: transparent;border-radius: 10px;"
                    v-model="levelOfInjury">
              <!--<option class="item" value="volvo"><label>请选择</label></option>-->
              <option class="item" v-for="item in LevelOfHarm"><label>{{item}}</label></option>
            </select>
          </div>
        </div>
        <br/>
        <br/>

        <div class="row">
          <div class="col-md-3" style="text-align: right">
            <label class="lable2">责任认定比例</label>
          </div>
          <div class="col-md-5">
            <select class="section1" v-model="rateOfRespon"
                    style="font-size: 18px;background-color: transparent;border-radius: 10px;">
              <option class="item" v-for="item in RateOfRespon"><label>{{item}}</label></option>
            </select>
          </div>
        </div>
        <br/>
        <br/>

        <div class="row">
          <div class="col-md-3" style="text-align: right">
            <label class="lable2">诉求</label>
          </div>
          <div class="col-md-5">
            <select class="section1" style="font-size: 18px;background-color: transparent;border-radius: 10px;">
              <option class="item" value="volvo" v-model="Wanting">
                <label class="chooseselect">请选择</label>
              </option>
            </select>
          </div>
          <div class="col-md-2" style="text-align: left">
            <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#myModal2">+</button>
          </div>
        </div>
      </div>

      <div class="conform">
        <button class="button" @click="NextStep">下一步</button>
      </div>
      <!--疾病弹出框-->
      <div class="modal fade" id="myModal1" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
        <div class="modal-dialog" role="document">
          <div class="modal-content" style="width: 100%">
            <div class="modal-header">
              <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span>
              </button>
              <h4 class="modal-title" id="myModalLabel1">详细列表</h4>
            </div>

            <div class="modal-body" style="text-align: center;font-size: 16px;font-family: 'Microsoft YaHei'">
              <div v-for="n in num" style="margin-bottom: 10px">
                <table class="table-striped" style="width: 100%;margin-left: auto;margin-right: auto">
                  <tbody>
                  <tr>
                    <td>疾病类型：</td>
                    <td>
                      <select v-model="DiseaseKind[n]">
                        <option v-for="aa in DiseaseList.DiseaseKind">{{aa}}</option>
                      </select>
                    </td>
                    <td>疾病名称：</td>
                    <td>
                      <select v-model="DiseaseName[n]">
                        <option v-for="bb in selection(n)">{{bb}}</option>
                      </select>
                    </td>
                  </tr>
                  </tbody>
                </table>
              </div>
            </div>

            <div class="modal-footer" style="text-align: center">
              <button type="button" class="btn" @click="Add">添加</button>
              <button type="button" class="btn" @click="Sub">删除</button>
              <button type="button" class="btn" data-dismiss="modal">关闭</button>
            </div>
          </div>
        </div>
      </div>
      <!--诉求弹出框-->
      <div class="modal fade" id="myModal2" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
        <div class="modal-dialog" role="document">
          <div class="modal-content" style="width: 50%">
            <div class="modal-header">
              <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span>
              </button>
              <h4 class="modal-title" id="myModalLabel2">详细列表</h4>
            </div>
            <div class="modal-body" style="text-align: center;font-size: 18px;font-family: 'Microsoft YaHei'">
              <p v-for="item in RequireList">{{item}}</p>
            </div>
            <div class="modal-footer" style="text-align: center">
              <button type="button" class="btn">添加</button>
              <button type="button" class="btn">删除</button>
              <button type="button" class="btn" data-dismiss="modal">关闭</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "second",
    number: 1,
    data() {
      return {
        index: 0,
        // kind: [,1,2,3,4],//随意赋5个值，仅用来绑定,index=0去除
        // name: [,1,2,3,4],//随意赋5个值，仅用来绑定,index=0去除
        num: 4,//循环整数
        RequireList: [
          "诉求一",
          "诉求二",
          "诉求三",
          "诉求四",
          "诉求五",
          "诉求六",
        ],
        DiseaseList:
          {
            DiseaseKind: [],
            DiseaseName: []
          },
        LevelOfHarm: [
          "一级甲等（死亡）",
          "一级乙等（一级伤残）",
          "二级甲等（二级伤残）",
          "二级乙等（三级伤残）",
          "二级丙等（四级伤残）",
          "二级丁等（五级伤残）",
          "三级甲等（六级伤残）",
          "三级乙等（七级伤残）",
          "三级丙等（八级伤残）",
          "三级丁等（九级伤残）",
          "三级戊等（十级伤残）",
          "四级（其他后果）",
        ],
        RateOfRespon: [
          "被告完全责任（100%）",
          "被告主要责任（60%-90%）",
          "同等责任（50%）",
          "被告次要责任（20%-40%）",
          "被告轻微责任（10%）",
          "被告无责任（0%）"
        ],
          DiseaseKind: [],
          DiseaseName: [],
          levelOfInjury: '',
          rateOfRespon: '',
          Wanting: ''
      }
    },
    methods: {
      NextStep() {
        this.$router.push({path: 'fourthPage'})
        var params = {
          DiseaseKind: this.DiseaseKind,
          DiseaseName: this.DiseaseName,
          levelOfInjury: this.levelOfInjury,
          rateOfRespon: this.rateOfRespon,
          Wanting: this.Wanting,
          id:sessionStorage.getItem("id")
        };
        this.$axios({
          method: 'post',
          url: 'api/postThirdInfo',
          data: params
        })
      },
      Add() {
        this.num++
      },
      Sub() {
        if (this.num == 0) return;
        else {
          this.num--
        }
      },
      selection(n) {
        for (var i = 0; i < this.DiseaseList.DiseaseKind.length; i++) {
          if (this.DiseaseList.DiseaseKind[i] === this.DiseaseKind[n]) {
            return this.DiseaseList.DiseaseName[i];
          }
        }
      }
    },
    mounted() {
      this.$axios({
        method: 'post',
        url: 'api/getDiseaseList'
      })
        .then((res) => {
          this.DiseaseList = res.data.DiseaseList;
          // console.log("0:" + typeof  this.DiseaseList.DiseaseKind);
          // console.log("1:" + typeof this.DiseaseList.DiseaseName)
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
</script>

<style scoped>
  .info {
    height: 600px;
    width: 100%;
  }

  ul {
    font-size: 16px;
    text-align: center;
    margin-top: 30px;
    /*border-bottom: 1px solid grey;*/
  }

  li {
    list-style-type: none;
    display: inline;
    padding-left: 0px;
    cursor: pointer;
  }

  .info li.active,
  .info li:hover {
    background: #4781a4;
    color: #fffaf3;
    border-radius: 2px;
  }

  .lable2 {
    font-size: 20px;
    height: 100%;
    width: 50%;
    font-weight: lighter;
  }

  .section1 {
    font-size: 18px;
    height: 100%;
    width: 100%;
  }

  .item {
    font-size: 15px;
  }

  .container {
    margin-top: 70px;
  }

  .conform {
    margin: 80px auto;
  }

  button {
    display: inline-block;
    font-size: 18px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #3a8dbe;
    border: none;
    border-radius: 15px;
    box-shadow: 0 4px #999;
  }

  .btn {
    /*display: inline-block;*/
    font-size: 15px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #3a8dbe;
    border: none;
    border-radius: 10px;
    box-shadow: 0 2px #999;
  }

  select {
    width: 180px;
  }
</style>



