<template>
  <div id="content">
    <view-page>
      <!-- 过滤条件区 -->
      <template slot="filter-field">
        <!-- 搜索框 -->
        <input v-model="searchStr"placeholder="请输入搜索内容" style="height: 28px">
        <span class="glyphicon glyphicon-zoom-in" aria-hidden="true"></span>
        <!-- 状态过滤框 -->
        <select v-model="filterStatus" placeholder="选择案件状态" @change="changeStatus" style="height: 28px">
          <option label="选择案件状态(全部)" value=""></option>
          <option v-for="(status,index) in currentStatus" :key="status" :label="status" :value="statuses.indexOf(status)"></option>
        </select>
        起始时间:<input type="date" v-model="startTime" @change="changeStatus">
        结束时间:<input type="date" v-model="endTime" @change="changeStatus">
        <!--<button class="btn btn-primary" @click="currentPageContent">筛选</button>-->
      </template>
      <!-- 右按钮区 -->
      <template slot="right-field">
      </template>
      <!-- 表格区 -->
      <template slot="table">
        <div class="table-responsive">
          <table class="table table-bordered table-hover" >
            <thead class="tablehead">
              <tr>
                <th>登记时间</th>
                <th>倒计时(工作日)</th>
                <th>案件名称</th>
                <th>申请人</th>
                <th>被申请人</th>
                <th>案件状态</th>
                <th>操作</th>
                <th>详情</th>
                <th>推送</th>
              </tr>
            </thead>
            <tbody  v-for="(item,index) in data" class="tablebody">
            <tr >
              <td>{{new Date(item.date).toLocaleDateString()}}</td>
              <td :class="CountdownColors[ParaChange(item.countdown)]" style="text-align: center">{{item.countdown}}</td>
              <td>{{item.name}}</td>
              <td>{{item.applicant}}</td>
              <td>{{item.respondent}}</td>
              <td :class="statusColors[item.status]">{{statuses[item.status]}}</td>
              <td>
                <template>
                  <div v-if="statuses[item.status]=='调解中'">
                    <button class="btn btn-info" @click="Mediating(item)">进入调解</button>
                  </div>
                  <div v-else-if="statuses[item.status]=='结案中'">
                    <button class="btn btn-success" @click="ForHandler(item.caseId)">生成协议</button>
                    <button class="btn btn-success"  @click="judicialConfirm(item.caseId)">司法确认</button>
                  </div>
                  <div v-else>
                    <!--<el-button type="primary" size="small" round @click="myclick(scope.row)">补充资料</el-button>-->
                  </div>
                </template>
              </td>
              <td>
                <button class="btn btn-primary" @click="ForCaseDetail(item)">查看详情</button>
              </td>
              <td>
                <template>
                  <select placeholder="请选择" style="width: 100px;" v-model="index" >
                    <option v-for="item in pushes" :key="item" :label="item" :value="item"></option>
                  </select>
                  <button class="btn btn-primary" @click="push(index)">push</button>
                </template>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </template>
      <!--分页-->
      <template slot="pagination">
        共{{totalPages}}页
        <select name="" id="" v-model="currentPageSize" style="margin-left: 30px" @change="changeStatus">
          <option v-for="item in PageSizes" :value="item" :label="item"></option>
        </select>条/页

        <button class="btn btn-info" @click="PreviousPage" style="margin-left: 30px">上一页</button>
        <!--第{{currentPage}}页-->
        <button class="btn btn-info" @click="NextPage"  style="margin-right: 30px">下一页</button>

        前往第
        <input type="number" style="width:50px" v-model="currentPage" @change="currentPageContent">页
      </template>
    </view-page>

    <judicial-confirm-dialog :is-show="judicialConfirmShow" title="司法确认" @close="closeJudicialConfirm" @submit="submitJudicialConfirm">
      <br>
      <a  @click="downLoadFile('http://pe93soaos.bkt.clouddn.com/common/%E5%8F%B8%E6%B3%95%E7%A1%AE%E8%AE%A4%E7%94%B3%E8%AF%B7%E4%B9%A6.docx')">
        司法确认申请书模板下载</a>
      <br><br>
      <label class="label1">上传司法确认申请书</label>
      <input type="file"  multiple>
    </judicial-confirm-dialog>
  </div>

</template>

<script>
  import ViewPage from '../../../components/ViewPage'
  import Dialog from '../../../components/Dialog'
  export default {
    components: {
      ViewPage,
      judicialConfirmDialog: Dialog,
    },
    data() {
      return {
        data:[],
        filterStatus: '',
        startTime:'',
        endTime:'',
        searchStr: '',
        currentStatus:[ '调解中','调解失败', '结案中', '已结案','撤销调解'],
        statuses: ['申请中','等待分配', '调解中','调解失败', '结案中', '已结案', '撤销申请', '撤销调解','立案失败','等待受理'],
        statusColors: ['warning', 'warning', 'success','danger', 'success', 'info', 'info', 'info','danger','warning'],
        CountdownColors:['danger','info'],
        multipleSelection: [],
        currentPage: 1,
        currentPageSize: 10,
        PageSizes: [2, 5, 10],
        totalPages:1,
        // kinds: ['未安排', '线上调解', '线下调解'],
        pushes: ['申请人', '被申请人', '专家', '法院', '保险公司', '公安', '司法厅', '鉴定中心','信访','卫生局'],
        ArrayOfCountdown:[],
        judicialConfirmShow:false,
        tempCaseId:'',
      }
    },

    methods: {
      ParaChange(para){
        if(para<=5)
          return 0
        else
          return 1
      },
      ForCaseDetail(para) {
        let caseState = this.statuses[para.status];
        let caseName = para.name;
        let caseId = para.caseId;
        let route = this.$router.resolve({
          path: '/CaseDetailPage',
          query: {
            prePath: this.$route.path,
            caseName: caseName,
            caseState: caseState,
            caseId: caseId
          }
        });
        window.open(route.href, '_blank');
      },

      Mediating(para) {
        let caseName = para.name;
        let caseId = para.caseId;
        let route = this.$router.resolve({
          path: 'MediatingPage',
          query: {
            caseName: caseName,
            caseId:caseId
          }
        });
        window.open(route.href, '_blank');
      },

      refresh() {
        location.reload()
      },
      judicialConfirm(para){
        this.tempCaseId=para;
        this.judicialConfirmShow=true
      },
      closeJudicialConfirm(){
        this.judicialConfirmShow=false;
      },
      submitJudicialConfirm(t){
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/inqueryHospital',
          data:{

          }
        })
        this.judicialConfirmShow=false;
      },
      downLoadFile(para){
        // window.open(para,'_blank')
        window.location.href=para;
      },
      //分页
      PreviousPage(){
        if(this.currentPage>1) {
          this.currentPage = this.currentPage - 1;
          this.currentPageContent();
        }
      },
      NextPage(){
        if(this.currentPage<this.totalPages){
          this.currentPage=this.currentPage+1;
          console.log(this.currentPage)
          this.currentPageContent();
        }
      },
      changeStatus(){
        this.currentPage=1;
        this.currentPageContent();
      },
      //显示当前页内容
      currentPageContent(){
        if(this.startTime!=''&&this.endTime!=''&&this.startTime>this.endTime) {
          alert("起始时间不可大于结束时间")
          this.startTime=''
          this.endTime=''
          return;
        }
        if(this.currentPage<=0)
        {
          alert("页数必须大于0")
          this.currentPage=1;
        }
        this.$axios.post('https://www.wangj1106.top/DisputeWeb/mediator/GetMyMediationData',{
          id:sessionStorage.getItem('id'),
          size:this.currentPageSize,
          page:this.currentPage,
          filterStatus:this.filterStatus,
          startTime:this.startTime,
          endTime:this.endTime,
        })
          .then((res)=>{
            this.data=res.data.data.mediationHallDataFormList;
            this.totalPages=res.data.data.totalPages
            //将data中的applicant、数组转化成字符串
            this.data.forEach((item)=>{
              item.applicant=item.applicant.join('  ');
            })
            this.data.forEach((item)=>{
              item.respondent=item.respondent.join('  ');
            })
          })
          .catch((error)=>{
            console.log(error)
          })
      },
      push(e) {
        //  推送给e
        alert('已推送')
      }
    },
    mounted(){
      this.$axios.post('https://www.wangj1106.top/DisputeWeb/mediator/GetMyMediationData',{
        id:sessionStorage.getItem('id'),
        size:this.currentPageSize,
        page:this.currentPage,
        filterStatus:this.filterStatus,
        startTime:this.startTime,
        endTime:this.endTime,
      })
        .then((res)=>{
          this.data=res.data.data.mediationHallDataFormList;
          this.totalPages=res.data.data.totalPages
          //将data中的applicant、数组转化成字符串
          this.data.forEach((item)=>{
            item.applicant=item.applicant.join('  ');
          });
          this.data.forEach((item)=>{
            item.respondent=item.respondent.join('  ');
          })
        })
        .catch((error)=>{
          console.log(error)
        })
    },

  }
</script>

<style scoped>
  #content {
    padding-left: 8px;
    padding-right: 8px;
  }
  .table{
    margin-top: 10px;
  }
  .tablehead{
    font-size: 18px;
  }
  .tablebody{
    font-size: 16px;
  }
  button{
    border-radius: 5px;
  }
  label{
    font-size: 16px;
    color: #3a8dbe;
  }
  /*#index*/
</style>



