<template>
  <div id="register">
    <div>
      <h1 class="text-center">注册</h1>
    </div>
    <div>
      <div class="form-group">
        <div class="form-group">
          <label for="">手机号</label>
          <span class="form-error">{{ userErrors.errorText }}</span>
          <input class="form-control" type="phone" placeholder="11位手机号" v-model="phone" style="background: none">
        </div>
        <div class="form-group">
          <label for="">验证码</label>
          <input class="form-control" type="phone" placeholder="请输入验证码" v-model="vertifyCode"
                 style="background: none">
        </div>
        <button class="btn btn-primary" type="button">发送验证码</button>
      </div>
      <div class="form-group">
        <label for="">密码</label>
        <span class="form-error">{{ passwordErrors.errorText }}</span>
        <input class="form-control" type="password" placeholder="8-16位字母或数字" v-model="password"
               style="background: none">
      </div>
      <div class="form-group">
        <label for="">再次输入密码</label>
        <span class="form-error">{{passwordErrors.errorText2 }}</span>
        <input class="form-control" type="password" placeholder="8-16位字母或数字" v-model="password2"
               style="background: none">
      </div>
      <div class="text-center">
        <button class="btn btn-success" type="button" @click="register">提交</button>
        <br>
        <a href="/">已有账号？点我登录</a>
      </div>
    </div>
  </div>

</template>


<script>
  export default {
    data() {
      return {
        phone: '',
        password: '',
        password2: '',
        vertifyCode: '',
      }
    },
    mounted() {
      this.phone = ''
      this.password = ''
      this.password2 = ''
      this.vertifyCode = ''
    },
    computed: {
      userErrors() {
        let errorText, status
        if (!/^\d{11}$/g.test(this.phone)) {
          status = false
          errorText = '请输入正确的手机号'
        }
        else {
          status = true
          errorText = ''
        }
        if (!this.userFlag) {
          errorText = ''
          this.userFlag = true
        }
        return {
          status,
          errorText
        }
      },
      passwordErrors() {
        let errorText, status, errorText2
        if (!/^\w{8,16}$/g.test(this.password)) {
          status = false
          errorText = '密码不是8-16位'
        }
        else {
          status = true
          errorText = ''
        }
        if (!this.passwordFlag) {
          errorText = ''
          this.passwordFlag = true
        }
        if (this.password != this.password2)
          errorText2 = '两次密码不一致'
        else
          errorText2 = ''
        return {
          status,
          errorText,
          errorText2
        }
      },
    },
    methods: {
      register() {
        if (this.phone == '')
          this.$alert("请填写手机号")
        else if (this.vertifyCode == '') {
          this.$alert("请输入验证码");
        }
        else if (this.password == '') {
          this.$alert("请输入密码");
        }
        else if (this.password != this.password2)
          this.passwordErrors().errorText2 = '两次密码不一致';
        else {
          let data = {phone: this.phone, password: this.password};
          this.$axios.post('https://www.wangj1106.top/user/register', data)
            .then((res) => {
              if (res.data.code == '0') {
                this.$alert('注册成功！', '提示', {
                  confirmButtonText: '确定',
                }).then(()=>{
                  this.$router.push({path: '/'});
                })
              }
              else {
                this.$alert('注册失败，用户已存在！', '提示', {
                  confirmButtonText: '确定',
                })
              }
            })
            .catch((error) => {
              console.log(error)
            })
        }
      },
    },
  }
</script>


<style>
  #register {
    border-style: ridge;
    border-radius: 10px;
    width: 700px;
    padding-left: 20px;
    padding-right: 20px;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
  }

  .form-error {
    color: red;
    padding-left: 5px;
  }
</style>



