<template>
  <div style="width: 100%;margin-left: auto;margin-right: auto">
    <div class="daohang">
      <ul class="list-unstyled">
        <router-link :to="'progress'" tag="li" active-class="active">案件进展</router-link>
        <!--<router-link :to="'JudiciaryCasePage'" tag="li" active-class="active">统计管理</router-link>-->
        <router-link :to="'authorityAllocate'" tag="li" active-class="active">权限管理</router-link>
        <router-link :to="'expertPage'" tag="li" active-class="active">专家咨询管理</router-link>
      </ul>
    </div>

    <div class="content" >
      <div v-if="this.role==2 && isLogin">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </div>
      <div v-else style="margin-top: 100px;text-align: center;font-size: 20px">
        {{txt}}
      </div>
    </div>

  </div>

</template>

<script>
  import LoginForm from '../../components/loginForm'

  export default {
    name: "manager",
    data() {
      return {
        name:sessionStorage.getItem("name"),
        role :sessionStorage.getItem("role"),
        id:sessionStorage.getItem("id"),
        isLogin:sessionStorage.getItem("isLogin"),
        txt:'请先登录！',
      }
    },
    mounted() {
      console.log(this.role!=2 && this.isLogin )
      console.log(this.isLogin)
      console.log()
      if(this.role!=2 && this.isLogin)
      {
        console.log(11111111)
        this.txt='请用管理员账号登录!'
      }
      console.log(this.txt)
    },
    components: {
      LoginForm
    },
    methods:{}
  }
</script>

<style scoped>
  .content {
    margin-top: 10px;
  }

  .daohang {
    background: none;
    padding: 10px 15px;
    margin-bottom: 25px;
    font-size: 16px;
  }

  .daohang li {
    text-align: left;
    padding: 10px 15px;
    cursor: pointer;
    float: left;
  }

  .daohang li.active,

  .daohang li:hover {
    background: #5ec4e2;
    color: #fff;
    border-radius: 15px;
  }
</style>



