<template>
  <div style="width: 100%;margin-left: auto;margin-right: auto">
    <div class="daohang">
      <ul class="list-unstyled">
        <li><a href="/RegConflictPage" target="_blank" style="color: #060606">纠纷登记</a></li>
        <router-link :to="'MediationHallPage'" tag="li" active-class="active">调解大厅</router-link>
        <router-link :to="'myMediation'" tag="li" active-class="active">我的调解</router-link>
        <!--与管理员的统计管理页面一致-->
        <!--<router-link :to="'JudiciaryCasePage'" v-if="authorityJudiciary==1" tag="li" active-class="active">-->
          <!--上送司法厅-->
        <!--</router-link>-->
      </ul>
    </div>

    <div class="content">
      <div v-if="this.role==1 && isLogin">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </div>
      <div v-else style="margin-top: 100px;text-align: center;font-size: 20px">
        {{txt}}
      </div>
    </div>
  </div>
</template>

<script>
  import LoginForm from '../../components/loginForm'

  export default {
    data() {
      return {
        phone:sessionStorage.getItem("phone"),
        role :sessionStorage.getItem("role"),
        id:sessionStorage.getItem("id"),
        isLogin:sessionStorage.getItem("isLogin"),
        authorityJudiciary: 0,
        txt:'请先登录！'
      }
    },
    mounted() {
      this.isLogin=sessionStorage.getItem("isLogin");
      if(this.role!=1 && this.isLogin)
      {
        this.txt='请用调解员账号登录！'
      }
      //获取当前调解员的权限
      this.$axios.post('https://www.wangj1106.top/DisputeWeb/Mediator/getAuthority', {id: this.id})
        .then((res) => {
            let authorityConfirm;
            if (res.data.data.authorityConfirm == 1)
              authorityConfirm = 1;
            else
              authorityConfirm = 0;
            sessionStorage.setItem("authorityConfirm", authorityConfirm);
            this.authorityJudiciary = res.data.data.authorityJudiciary
          }
        )
        .catch((error) => {
          console.log(error)
        })
    },
    components: {
      LoginForm
    }
    ,
    methods: {}
  }
</script>

<style scoped>
  .content {
    margin-top: 20px;
    /*border-radius: 20px;*/
    /*border-style: ridge;*/
    /*height: 750px;*/
    /*padding-top: 10px;*/
  }
  .daohang {
    background: none;
    padding: 10px 15px;
    margin-bottom: 25px;
    font-size: 16px;
  }

  .daohang li {
    text-align: left;
    padding: 10px 15px;
    cursor: pointer;
    float: left;
  }

  .daohang li.active,
  .daohang li:hover {
    background: #5ec4e2;
    color: #fff;
    border-radius: 15px;
  }
</style>



