<template>
  <div>
    <div class="showBoard">
      <ul class="list-unstyled">
        <router-link v-for="item in show" :to="{ path: item.path,param:{name}}" tag="li" active-class="active">
          {{ item.name }}
        </router-link>
      </ul>
    </div>
    <div class="content">
      <div class="container">
        <div class="row">
          <div class="col-lg-1 col-md-1 col-sm-1 col-xs-1">
            <div id="user">
              <img src="../../assets/icon/user.jpg" alt="" style="width: 100px;height: 100px">
              <br>
              <label>用户名:</label>{{phone}}
              <br>
              <label>个人信息:</label>{{infro}}
            </div>
          </div>
          <div class="col-lg-11 col-md-11 col-sm-11 col-xs-11">
            <div id="show">
              <div v-if="isLogin">
                <keep-alive>
                  <router-view></router-view>
                </keep-alive>
              </div>
              <div v-else style="margin-top: 100px;text-align: center;font-size: 20px">
                请先登录！
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import LoginForm from '../../components/loginForm'
  import RegisterForm from '../../components/registerForm'


  export default {
    data() {
      return {
        role:sessionStorage.getItem("role"),
        phone:sessionStorage.getItem("phone"),
        id:sessionStorage.getItem("id"),
        isLogin:sessionStorage.getItem("isLogin"),
        infro: 'xxxxxx',
        show: [
          {
            name: '咨询历史',
            path: 'history',
            active: true
          },
          {
            name: '案件进展',
            path: 'userProcess',
            active: false
          },
          {
            name: '通知中心',
            path: 'message',
            active: false
          }
        ]
      }
    },
    components: {
      RegisterForm,
      LoginForm,
    },
    mounted() {
    },
    methods: {
      onClickMessage() {
        this.$router.push({path: '/userCenter/message'});
      },

    }
  }
</script>

<style scoped>
  .container {
    /*margin-top: 20px;*/
    width: 100%;
  }

  .content {
    margin-top: 1px;
    padding-top: 10px;
  }

  #user {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30%;
  }

  #show {
    margin-top: 10px;
    /*border-radius: 20px;*/
    /*border-style: ridge;*/
    height: 720px;
  }

  .showBoard {
    background: none;
    padding: 15px 0;
    margin-left: 9%;
  }

  .showBoard li {
    text-align: left;
    padding: 8px 10px;
    cursor: pointer;
    float: left;
    margin-right: 5px;
  }

  .showBoard li.active,
  .showBoard li:hover {
    background: #3a8dbe;
    color: #fff;
    border-radius: 15px;
  }

  label {
    font-size: 15px;
  }

</style>



