<template>
  <div id="content">
    <view-page>
      <!-- 左按钮区 -->
      <!--<template slot="left-field">-->
        <!--<el-button type="primary" style="width: 90px">上传司法厅</el-button>-->
      <!--</template>-->
      <!-- 搜索框 -->
      <!--<template slot="search-field">-->
        <!--<input v-model="searchStr"placeholder="请输入搜索内容" style="height: 28px">-->
      <!--</template>-->
      <!-- 过滤条件区 -->
      <template slot="filter-field">
        <!-- 搜索框 -->
        <input v-model="searchStr"placeholder="请输入搜索内容" style="height: 28px">
        <span class="glyphicon glyphicon-zoom-in" aria-hidden="true"></span>
        <!-- 状态过滤框 -->
        <select v-model="filterStatus" placeholder="选择案件状态(全部)" @change="changeStatus" style="height: 28px">
          <option label="选择案件状态(全部)" value=""></option>
          <option v-for="(status,index) in statuses" :key="status" :label="status" :value="index"></option>
        </select>
        <!--调解员过滤框-->
        <select v-model="filterMediator" placeholder="选择调解员(全部)" @change="changeStatus" style="height: 28px">
          <option label="选择调解员(全部)" value=""></option>
          <option v-for="(mediator,index) in AllMediator" :key="mediator" :label="mediator"
                     :value="mediator"></option>
        </select>
        <!-- 时间过滤框 -->
        起始时间:<input type="date" v-model="startTime" @change="changeStatus">
        结束时间:<input type="date" v-model="endTime" @change="changeStatus">
        <!--<button class="btn btn-primary" @click="currentPageContent">筛选</button>-->
        <button></button>
      </template>
      <!-- 右按钮区 -->
      <template slot="right-field">
        <button class="btn btn-primary">导出</button>
        <button class="btn btn-primary" >导入</button>
        <button class="btn btn-primary">上传司法厅</button>
      </template>
      <!-- 表格区 -->
      <template slot="table">
        <div class="table-responsive">
          <table class="table table-bordered table-hover ">
            <thead class="tablehead">
            <tr>
              <th>案件时间</th>
              <th>案件名称</th>
              <th>申请人</th>
              <th>被申请人</th>
              <th>案件状态</th>
              <th>调解员</th>
              <th>登记信息</th>
            </tr>
            </thead>
            <tbody  v-for="(item) in data" class="tablebody">
            <tr>
              <td>{{new Date(item.date).toLocaleDateString()}}</td>
              <td>{{item.name}}</td>
              <td>{{item.applicant}}</td>
              <td>{{item.respondent}}</td>
              <td :class="statusColors[item.status]">{{statuses[item.status]}}</td>
              <td >{{item.currentMediator}}</td>
              <td>
                <button class="btn btn-primary" @click="ForCaseDetail(item)">查看详情</button>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </template>
      <br>
      <!--分页-->
      <template slot="pagination">
        共{{totalPages}}页
        <select name="" id="" v-model="currentPageSize" style="margin-left: 30px" @change="currentPageContent">
          <option v-for="item in PageSizes" :value="item" :label="item"></option>
        </select>条/页

        <button class="btn btn-info" @click="PreviousPage" style="margin-left: 30px">上一页</button>
        <!--第{{currentPage}}页-->
        <button class="btn btn-info" @click="NextPage"  style="margin-right: 30px">下一页</button>

        前往第
        <input type="number" style="width:50px" v-model="currentPage">页
      </template>
    </view-page>
  </div>
</template>

<script>
  import ViewPage from '../../components/ViewPage'

  export default {
    components: {
      ViewPage,
    },
    data() {
      return {
        data: [],
        AllMediator:[],
        filterStatus: '',
        filterMediator: '',
        searchStr: '',
        statuses: ['申请中','等待分配', '调解中','调解失败', '结案中', '已结案', '撤销申请', '撤销调解','立案失败'],
        statusColors: ['warning', 'warning', 'success','danger', 'success', 'info', 'info', 'info','danger'],
        // sortProp: '',
        // sortOrder: 'descending',
        multipleSelection: [],

        currentPage: 1,
        currentPageSize: 10,
        PageSizes: [2, 5, 10],
        totalPages:1,
        startTime:'',
        endTime:'',
      }
    },
    mounted() {
      //获取案件列表
      this.$axios.post('https://www.wangj1106.top/DisputeWeb/manager/getCase_judiciary',{
        id:sessionStorage.getItem("id"),
        size:this.currentPageSize,
        page:this.currentPage,
        filterStatus:this.filterStatus,
        filterMediator:this.filterMediator,
        startTime:this.startTime,
        endTime:this.endTime,
      })
        .then((res) => {
          this.data = res.data.data;
          this.totalPages=res.data.data.totalPages;
          //将data中的applicant、数组转化成字符串
          this.data.forEach((item)=>{
            item.applicant=item.applicant.join('  ');
          })
          this.data.forEach((item)=>{
            item.respondent=item.respondent.join('  ');
          })
        })
        .catch((error) => {
          console.log(error)
        });
      //获取所有调解员,用于筛选
      this.$axios.get('https://www.wangj1106.top/DisputeWeb/manager/getAllMediator',)
        .then((res) => {
          this.AllMediator = res.data.data
        })
        .catch((error) => {
          console.log(error)
        });
    },
    methods: {
      ForCaseDetail(para) {
        let caseState = this.statuses[para.status];
        let caseName = para.name;
        let caseId = para.caseId;
        let route = this.$router.resolve({
          path: '/CaseDetailPage',
          query: {
            prePath: this.$route.path,
            caseName: caseName,
            caseState: caseState,
            caseId: caseId
          }
        });
        window.open(route.href, '_blank');
      },
      refresh() {
        location.reload()
      },

      // sortChange(column) {
      //   this.sortProp = column.prop;
      //   this.sortOrder = column.order
      // },   // 排序

      //起始时间控制
      timeControl(){
        // console.log(this.startTime>this.endTime)
        // if(this.startTime!=''&&this.endTime!=''&&this.startTime>this.endTime) {
        //   console.log(this.startTime>this.endTime)
        //   alert("起始时间不可大于结束时间")
        //   this.startTime=''
        //   this.endTime=''
        // }
      },
      //分页
      PreviousPage(){
        if(this.currentPage>1) {
          this.currentPage = this.currentPage - 1;
          this.currentPageContent();
        }
      },
      NextPage(){
        if(this.currentPage<this.totalPages){
          this.currentPage=this.currentPage+1;
          console.log(this.currentPage)
          this.currentPageContent();
        }
      },
      changeStatus(){
        this.currentPage=1;
        this.currentPageContent();
      },
      currentPageContent(){
        if(this.startTime!=''&&this.endTime!=''&&this.startTime>this.endTime) {
          alert("起始时间不可大于结束时间")
          this.startTime=''
          this.endTime=''
          return;
        }
        this.$axios.post('https://www.wangj1106.top/DisputeWeb/manager/getCase_judiciary',{
          id:sessionStorage.getItem("id"),
          size:this.currentPageSize,
          page:this.currentPage,
          filterStatus:this.filterStatus,
          filterMediator:this.filterMediator,
          startTime:this.startTime,
          endTime:this.endTime,
        })
          .then((res) => {
            this.data = res.data.data;
            this.totalPages=res.data.data.totalPages;
            //将data中的applicant、数组转化成字符串
            this.data.forEach((item)=>{
              item.applicant=item.applicant.join('  ');
            })
            this.data.forEach((item)=>{
              item.respondent=item.respondent.join('  ');
            })
          })
          .catch((error) => {
            console.log(error)
          });
      }
    },

  }
</script>

<style scoped>
  #content {
    padding-left: 8px;
    padding-right: 8px;
  }
  .table{
    margin-top: 10px;
  }
  .tablehead{
    font-size: 18px;
  }
  .tablebody{
    font-size: 16px;
  }
  button{
    border-radius: 10px;
  }
</style>



