<template>
  <div class="info">
    <ul>
      <router-link to="firstPage" tag="li" active-class="active">简要情况</router-link>
      <router-link to="secondPage" tag="li" active-class="active">基本信息|</router-link>
      <router-link to="thirdPage" tag="li" active-class="active">纠纷概况|</router-link>
      <router-link to="fourthPage" tag="li" active-class="active">医疗行为</router-link>
    </ul>
    <button class="button" @click="recognition" style="width: 100px;margin-bottom: 5px">语音录入</button>
    <br>

    <div style="position:relative;">
      <textarea rows="20" style="width: 80%;background-color: rgba(239,234,228,0.5);border:rgb(236,255,240) 1px solid;"
                placeholder="简要描述情况" v-model="content"></textarea>
    </div>

    <div class="conform">
      <button class="button" @click="NextStep">下一步</button>
      <button class="button" @click="NextStep2">跳过</button>
    </div>
  </div>

</template>

<script>
  import speechRecognition from '../../../assets/js/SpeechRecogniton'
  export default {
    name: "first",
    data() {
      return {
        content:'',
      }
    },
    methods: {
      NextStep() {
        this.$router.push({path: 'KeyWordPage'})
        var params={
          SimpleContent:this.content,
          id:sessionStorage.getItem("id")
        };
        this.$axios({
          method:'post',
          url:'api/postSimpleContent',
          data:params
        })
      }
      ,
      NextStep2() {
        this.$router.push({path: 'second'})
      }
      ,
      recognition() {
        speechRecognition.start;
        this.content=speechRecognition.content;
      }
    },
  }
</script>

<style scoped>
  .info {
    height: 600px;
    width: 100%;
  }

  ul {
    font-size: 16px;
    text-align: center;
    margin-top: 30px;
  }

  li {
    list-style-type: none;
    display: inline;
    padding-left: 0px;
    cursor: pointer;
  }

  .info li.active,
  .info li:hover {
    background: #4781a4;
    color: #fffaf3;
    border-radius: 2px;
  }

  .conform {
    margin-top: 20px;
  }

  .button {
    display: inline-block;
    font-size: 18px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #3a8dbe;
    border: none;
    border-radius: 15px;
    box-shadow: 0 4px #999;
    width: 80px;
  }
</style>



