<template>
  <div style="text-align: center">
    <div class="info">
      <ul>
        <router-link to="firstPage" tag="li" active-class="active">简要情况</router-link>
        <router-link to="secondPage" tag="li" active-class="active">基本信息|</router-link>
        <router-link to="thirdPage" tag="li" active-class="active">纠纷概况|</router-link>
        <router-link to="fourthPage" tag="li" active-class="active">医疗行为</router-link>
      </ul>

      <div class="container">
        <div class="BehaviorGroup" v-for="(item,index) in list" style="margin-bottom: 20px">
          <div class="row">
            <div class="col-md-3">
              <button @click="myClick(index)">{{list[index].name}}</button>
            </div>
          </div>
          <div v-if="list[index].btn">
            <div v-for="n in list[index].number" style="margin-bottom: 10px">
              <div class="row">
                <div class="col-md-2">
                  <label></label>
                </div>
                <div class="col-md-2 ">
                  <label class="lable2">{{list[index].subname}}</label>
                </div>
                <div class="col-md-2">
                  <select class="section1" v-model="ResultList[index].subname[n]">
                    <option class="item" v-for="x in list[index].subnamelist"><label>{{x}}</label></option>
                  </select>
                </div>
                <div class="col-md-2">
                  <label class="lable2">过失类型</label>
                </div>
                <div class="col-md-2">
                  <select class="section1" v-model="ResultList[index].FaultType[n]">
                    <option v-for="kind  in list[index].faultKind">{{kind}}</option>
                  </select>
                </div>
              </div>
              <div class="row">
                <div class="col-md-2">
                  <label></label>
                </div>
                <div class="col-md-2 ">
                  <label class="lable2">造成后果</label>
                </div>
                <div class="col-md-2">
                  <select class="section1"v-model="ResultList[index].Result[n]">
                    <option class="item" v-for="x in list[index].resultlist"><label>{{x}}</label></option>
                  </select>
                </div>
                <div class="col-md-2">
                  <label class="lable2">其他信息</label>
                </div>
                <div class="col-md-2">
                  <input style="font-size: 16px;background-color: transparent;border-radius: 10px;width: 100%" v-model="ResultList[index].OtherInfo[n]"
                         placeholder="请输入其他过失类型">
                </div>
              </div>
            </div>

          </div>
          <div style="float: right">
            <span><button @click="AddNumber(index)" style="width: 30px">+</button></span>
            <span><button @click="SubNumber(index)" style="width: 30px">-</button></span>
          </div>

        </div>

      </div>

    </div>
    <br>
    <div class="conform">
      <button class="btn" @click="NextStep">下一步</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "four",
    data() {
      return {
        list: [
          {
            name: "手术",
            subname: "手术名称",
            subnamelist:[],
            resultlist:[],
            btn: true,
            number: 1,
            faultKind: [
              "手术操作失误",
              "遗留异物于体内",
              "手术时机、方案与病理决策不妥",
              "手术治疗不彻底/手术未治疗疾病",
              "手术导致组织器官损伤",
              "手术部位感染",
              "并发症：请输入并发症名称"],
          },
          {
            name: "诊断",
            subname: "诊断内容",
            subnamelist:[],
            resultlist:[],
            btn: false,
            number: 1,
            faultKind: [
              "错误诊断",
              "遗漏诊断",
              "延误诊断",
              "其他"]
          },
          {
            name: "检验",
            subname: "检验内容",
            subnamelist:[],
            resultlist:[],
            btn: false,
            number: 1,
            faultKind: [
              "检验操作失误",
              "数据报告错误",
              "其他"]
          },
          {
            name: "用药",
            subname: "药品名称",
            subnamelist:[],
            resultlist:[],
            btn: false,
            number: 1,
            faultKind: [
              "药品适应症与禁忌",
              "药品剂量",
              "其他"]
          },
          {
            name: "管理",
            btn: false,
            subname: "管理内容",
            subnamelist:[],
            resultlist:[],
            number: 1,
            faultKind: [
              "医院资质、制度",
              "医院设施与医疗器械",
              "护理",
              "医患沟通与隐私",
              "文书书写与保管",
              "其他"]
          },
          {
            name: "其他",
            subname: "其他内容",
            subnamelist:[],
            resultlist:[],
            btn: false,
            number: 1,
            faultKind: []
          }
        ],
        ResultList:[
          {
            name:'手术',
            subname:[],
            FaultType:[],
            Result:[],
            OtherInfo:[],
          },
          {
            name:'诊断',
            subname:[],
            FaultType:[],
            Result:[],
            OtherInfo:[],
          },
          {
            name:'检验',
            subname:[],
            FaultType:[],
            Result:[],
            OtherInfo:[],
          },
          {
            name:'用药',
            subname:[],
            FaultType:[],
            Result:[],
            OtherInfo:[],
          },
          {
            name:'管理',
            subname:[],
            FaultType:[],
            Result:[],
            OtherInfo:[],
          },
          {
            name:'其他',
            subname:[],
            FaultType:[],
            Result:[],
            OtherInfo:[],
          }
        ]
      }
    },
    methods: {
      myClick(index) {
        this.list[index].btn = !this.list[index].btn
        if (this.list[index].number == 0) {
          this.list[index].btn = true;
          this.list[index].number++;
        }
      },
      NextStep() {
        this.$router.push({path: 'KeyWordPage'});

        this.$axios({
          method:'post',
          url:'api/getFourthPage',
          data:{ResultList:this.ResultList,id:sessionStorage.getItem("id")}
        })
      },
      AddNumber(index) {
        this.list[index].number++;
        this.list[index].btn = true;
        if(this.list[index].number==1)
          this.list[index].btn = true;
      },
      SubNumber(index) {
        if (this.list[index].number==0) return;
        else {
          this.list[index].number--;
          if (this.list[index].number == 0)
            this.list[index].btn = false;
        }
      },
    },

    mounted() {
      this.$axios({
        method: 'get',
        url: 'api/getFourthPage',
      })
        .then((res) => {
          console.log(res.data)
          for (let index=0;index<6;index++){
            console.log(index)

            this.list[index].subnamelist = res.data.list[index].subnamelist;
            this.list[index].resultlist = res.data.list[index].resultlist;
            this.list[index].faultKind = res.data.list[index].faultKind;
          }
          console.log(this.list)

        })
        .catch((error) => {
          console.log(error)
        });
    },
  }
</script>

<style scoped>
  .info {
    height: 600px;
    width: 100%;
    overflow-x: hidden;
    overflow: scroll;
  }

  ul {
    font-size: 16px;
    text-align: center;
    margin-top: 30px;
    /*border-bottom: 1px solid grey;*/
  }

  li {
    list-style-type: none;
    display: inline;
    padding-left: 0px;
    cursor: pointer;
  }

  .info li.active,
  .info li:hover {
    background: #4781a4;
    color: #fffaf3;
    border-radius: 2px;
  }

  .lable1 {
    width: 100%;
    height: 100%;
    font-size: 20px;
    font-weight: lighter;
  }

  .lable2 {
    width: 100%;
    height: 100%;
    font-size: 18px;
    font-weight: lighter;
  }

  .section1 {
    height: 100%;
    width: 100%;
    font-size: 18px;
    background-color: transparent;
    border-radius: 10px;
  }

  .item {
    font-size: 15px;
  }

  .container {
    margin-top: 40px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }

  .conform {
    margin-top: 20px;
    margin-bottom:50px;
  }

  button {
    display: inline-block;
    /*padding: 15px 25px;*/
    font-size: 18px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #3a8dbe;
    border: none;
    border-radius: 15px;
    box-shadow: 0 4px #999;
  }

  .btn {
    font-size: 16px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #3a8dbe;
    border: none;
    border-radius: 15px;
    box-shadow: 0 4px #999;
    /*bottom: 0px;*/
    /*position: absolute;*/
  }
</style>



