<template>
  <div id="detail">
    <div>
      <label for="">1.问题类型</label>
      <br>
      <div class="container">
        <div class="row">
          <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3" v-for="item in kinds" >
            <input type="checkbox" :value="item.name" v-model="problem">
            <label>{{item.name}}</label>
          </div>
        </div>
      </div>
    </div>
    <hr>
    <div>
      <span><label for="">2.所在地区</label></span>

      <form role="form">
        <div class="form-group">
          <select class="form-control" v-model="location">
            <option v-for="item in locals" :value="item.value">{{item.name}}</option>
          </select>
        </div>
      </form>
    </div>
    <hr>
    <div>
      <label for="">具体问题描述</label>
      <br>
      <div style="text-align: center">
        <textarea name="" id="" col="" rows="6" style="border-style:ridge;background:none;width: 99%"
                  placeholder="详述"></textarea>
      </div>
    </div>
    <hr>
  </div>
</template>

<script>
  export default {
    name: "word-consult",
    data() {
      return {
        location: '0',
        problem:[],
        kinds:[
          {
            name:'婚姻类型'
          },
          {
            name:'1xx类型'
          },
          {
            name:'2xx类型'
          },
          {
            name:'3xx类型'
          },
          {
            name:'4xx类型'
          },
          {
            name:'5xx类型'
          },
          {
            name:'6xx类型'
          },
          {
            name:'7xx类型'
          },
          {
            name:'8xx类型'
          }
        ],
        locals: [
          {
            name: '鼓楼区',
            value: 0
          },
          {
            name: '江宁区',
            value: 1
          },
          {
            name: '浦口区',
           value: 2
          },
          {
            name: '仙林区',
            value: 3
          }
        ]
      }
    }
  }
</script>

<style scoped>
  #detail {
    margin: 40px 30px;
  }
</style>



