</template>

<script>
  import ViewPage from '../../components/ViewPage'
  import Dialog from '../../components/Dialog'
  import Vue from 'Vue'
  export default {
    name: "progress",
    components: {
      ViewPage,
      AllocateDialog: Dialog,
      AcceptApplyDialog: Dialog,
      RefuseApplyDialog: Dialog,
      AdviseDialog: Dialog,
      InquireDialog:Dialog,
      AppendAdvocateDialog: Dialog
    },
    data() {
      return {
        data: [],
        AllMediator:[],
        MediatorList: [],
        filterStatus: '',
        filterMediator: '',
        searchStr: '',
        CountdownColors:['danger','info','none'],
        statuses: ['申请中','等待分配', '调解中','调解失败', '结案中', '已结案', '撤销申请', '撤销调解','立案失败','等待受理'],
        currentStatus: ['申请中','等待受理','等待分配', '调解中','调解失败', '结案中', '已结案', '撤销申请', '撤销调解','立案失败'],
        statusColors: ['warning', 'warning', 'success','danger', 'success', 'info', 'info', 'info','danger','warning'],

        multipleSelection: [],

        currentPage: 1,
        currentPageSize: 10,
        PageSizes: [2, 5, 10],
        totalPages:1,

        currentPage2: 1,
        currentPageSize2: 10,
        PageSizes2: [2,5,10],
        totalPages2:1,

        allocateShow: false,
        acceptApplyShow: false,
        refuseApplyShow: false,
        adviseShow: false,
        appendallocateShow: false,

        tempCaseId:'',
        tempUserIntention: [],

        radio: '',
        startTime:'',
        endTime:'',
        reasonForApply:'',
        reasonForRefuse:'',
        advise:'',
        result:'',
        applyFile:{
          files:['http://pe93soaos.bkt.clouddn.com/common/%E5%8D%97%E4%BA%AC%E5%B8%82%E5%8C%BB%E6%82%A3%E7%BA%A0%E7%BA%B7%E4%BA%BA%E6%B0%91%E8%B0%83%E8%A7%A3%E5%A7%94%E5%91%98%E4%BC%9A%EF%BC%88%E5%91%8A%E7%9F%A5%E4%B9%A6%EF%BC%89%E6%9C%89%E6%9D%83%E5%88%A9%E3%80%81%E4%B9%89%E5%8A%A1.docx']
        },
        inquireShow: false,
        inquireResult: '',
        inquireHistory: [{
          hospital:'鼓楼医院',
          inquireDate:'2018/1/2',
          mediatorName:'张三',
          inquireResult:'拒绝调解',
          inquireReplyPerson:'李四',
          otherMessage:'与事实不符',
          // file: [
          //   {url: 'http://pe93soaos.bkt.clouddn.com/基于Hadoop的在线网络日志分析系统研究.pdf'},
          //   // {url: 'http://pe93soaos.bkt.clouddn.com/基于Hadoop的在线网络日志分析系统研究.pdf'},
          //   {url:"http://pe93soaos.bkt.clouddn.com/common/%E5%8D%97%E4%BA%AC%E5%B8%82%E5%8C%BB%E6%82%A3%E7%BA%A0%E7%BA%B7%E4%BA%BA%E6%B0%91%E8%B0%83%E8%A7%A3%E5%A7%94%E5%91%98%E4%BC%9A%EF%BC%88%E5%91%8A%E7%9F%A5%E4%B9%A6%EF%BC%89%E6%9C%89%E6%9D%83%E5%88%A9%E3%80%81%E4%B9%89%E5%8A%A1.docx"}
          // ],
          inquireContent: 'dadghbwsdfbvskdjvbndskvbjshbdxfggggghfghfghfg' +
            'hfghhhhhhhhhhhhhhhhhhhhhh' +
            'hfgggggggggggggggggggggg' +
            'hfgggggggggggggggggva',
        }],
        tempText:'',
        instituteMessage:[{
          name:'',
          fixedTele:'',
          contactPerson:'',
          contactPhone:''
        },
        ],
        isFinished:'',
        inquireMessage: [
          {
            inquireDate:'',
            inquireReplyPerson:'',
            inquireResult:'',
            otherMessage:'',
          },
        ],
        file:'',
        currentInsititue:0,
      }
    },
    mounted() {
      //请求案件进展列表
      this.$axios.post('https://www.wangj1106.top/DisputeWeb/manager/getCaseList',
        {
          size:this.currentPageSize,
          page:this.currentPage,
          filterStatus:this.filterStatus,
          filterMediator:this.filterMediator,
          startTime:this.startTime,
          endTime:this.endTime,
        })
        .then((res) => {
          this.data = res.data.data.CaseList;
          this.totalPages=res.data.data.totalPages;
          //将data中的applicant、数组转化成字符串
          this.data.forEach((item)=>{
            item.applicant=item.applicant.join('  ');
          })
          this.data.forEach((item)=>{
            item.respondent=item.respondent.join('  ');
          })
          this.startTime='';
          this.endTime='';
        })
        .catch((error) => {
          console.log(error)
        });
      //获取所有调解员，用于筛选
      this.$axios.get('https://www.wangj1106.top/DisputeWeb/manager/getAllMediator',)
        .then((res) => {
          this.AllMediator = res.data.data
        })
        .catch((error) => {
          console.log(error)
        });
    },
    methods: {
      ParaChange(para){
        if(para=='')
          return 2
        if(para<=5)
          return 0
        else if(5<para<=10)
          return 1
        else
          return 2
      },
      ForCaseDetail(para) {
        let caseState = this.statuses[para.status];
        let caseName = para.name;
        let caseId = para.caseId;
        let route = this.$router.resolve({
          path: '/CaseDetailPage',
          query: {
            prePath: this.$route.path,
            caseName: caseName,
            caseState: caseState,
            caseId: caseId
          }
        });
        window.open(route.href, '_blank');
      },
      refresh() {
        location.reload()
      },
      changeInstitute(para){
        this.currentInsititue=para;
        console.log(this.currentInsititue)
      },
      inquireInstitute(para){
        this.tempCaseId=para.caseId;
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/getHospitalMessage',
          data:{
            caseId:this.tempCaseId,
          }
        })
          .then((res)=>{
            this.instituteMessage=res.data.data;
            this.inquireMessage.splice(0,1);
            for(let i=0;i<this.instituteMessage.length;i++) {
              let temp =
                {
                  hospital: this.instituteMessage[i].name,
                  inquireDate: '',
                  inquireReplyPerson: '',
                  inquireResult: '',
                  otherMessage: '',
                };
              this.inquireMessage.push(temp);
            }
          })
          .catch((error) => {
            console.log(error)
          })
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/getInqueryHospitalList',
          data:{
            caseId:this.tempCaseId,
          }
        })
          .then((res)=>{
            this.inquireHistory=res.data.data
            if(this.inquireHistory!=undefined){
              for(let i=0;i<this.inquireHistory.length;i++) {
                if(this.inquireHistory[i].inquireResult == "1")
                  this.inquireHistory[i].inquireResult = '接受调解';
                else
                  this.inquireHistory[i].inquireResult = '拒绝调解';
              }
            }
            this.inquireShow=true
          })
          .catch((error) => {
            console.log(error)
          })
      },
      closeInquire(){
        this.inquireShow=false;
      },
      submitInquire(){
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/inqueryHospital',
          data:{
            isFinished:this.isFinished,
            caseId:this.tempCaseId,
            mediatorId:sessionStorage.getItem("id"),
            inquireMessage:this.inquireMessage,
          }
        })
          .then((res) => {
            let nextStatus=0;
            if(this.isFinished==1)
              nextStatus=9;//等待受理
            if(this.isFinished==2)
              nextStatus=8; //立案失败
            if(res.data.code>=0)
            {
              let index = this.data.findIndex(item => {
                return item.caseId == this.tempCaseId
              })
              Vue.set(this.data,index,{
                date:this.data[index].date,
                name:this.data[index].name,
                caseId:this.data[index].caseId,
                status:nextStatus,
                applicant:this.data[index].applicant,
                respondent:this.data[index].respondent,
                currentMedaitor:this.data[index].currentMedaitor,
                mediatorid:this.data[index].mediatorid,
              })
            }
            this.inquireShow=false;
          })
          .catch((error) => {
            console.log(error)
          });

      },
      downLoadFile(para){
        // window.open(para,'_blank')
        window.location.href=para;
      },
      // // 排序
      // sortChange(column) {
      //   this.sortProp = column.prop
      //   this.sortOrder = column.order
      // },
      //起始时间控制
      // timeControl(){
      //   // console.log(this.startTime>this.endTime)
      //   if(this.startTime!=''&&this.endTime!=''&&this.startTime>this.endTime) {
      //     // console.log(this.startTime>this.endTime)
      //     alert("起始时间不可大于结束时间")
      //     this.startTime=''
      //     this.endTime=''
      //   }
      // },
      //分页1
      PreviousPage(){
        if(this.currentPage>1) {
          this.currentPage = this.currentPage - 1;
          this.currentPageContent();
        }
      },
      NextPage(){
        if(this.currentPage<this.totalPages){
          this.currentPage=this.currentPage+1;
          this.currentPageContent();
        }
      },
      changeStatus(){
        this.currentPage=1;
        this.currentPageContent();
      },
      //显示当前页内容
      currentPageContent(){
        if(this.startTime!=''&&this.endTime!=''&&this.startTime>this.endTime) {
          alert("起始时间不可大于结束时间")
          this.startTime=''
          this.endTime=''
          return;
        }
        if(this.currentPage<=0)
        {
          alert("页数必须大于0")
          this.currentPage=1;
        }
        //请求案件进展列表
        this.$axios.post('https://www.wangj1106.top/DisputeWeb/manager/getCaseList',
          {
            size:this.currentPageSize,
            page:this.currentPage,
            filterStatus:this.filterStatus,
            filterMediator:this.filterMediator,
            startTime:this.startTime,
            endTime:this.endTime,
          })
          .then((res) => {
            this.data = res.data.data.CaseList;
            this.totalPages=res.data.data.totalPages;
            if(this.totalPages==0)
              this.currentPage=0;
            //将data中的applicant、responden数组转化成字符串
            this.data.forEach((item)=>{
              item.applicant=item.applicant.join('  ');
            })
            this.data.forEach((item)=>{
              item.respondent=item.respondent.join('  ');
            })
          })
          .catch((error) => {
            console.log(error)
          });
      },
      //案件分配弹出框
      allocateTodo(e) {
        this.allocateShow = true;
        this.tempUserIntention = e.userIntention;
        this.tempCaseId=e.caseId;
      },
      closeDialogAppend() {
        this.appendallocateShow = false
      },
      submitAppend() {
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/manager/PostMediatorForCase',
          data:{
            MediatorId:this.radio,
            CaseId:this.tempCaseId
          }
        })
          .then((res) => {
            console.log(typeof(res.data.code))
            console.log(res.data.code>=0)
            if(res.data.code>=0)
            {
              console.log(typeof(res.data.code))
              console.log(res.data.code>=0)
              let index=this.data.findIndex(item=>{
                return item.caseId==this.tempCaseId
              })
              Vue.set(this.data,index,{
                date:this.data[index].date,
                countdown:this.data[index].countdown,
                name:this.data[index].name,
                caseId:this.data[index].caseId,
                status:2,  //调解中
                applicant:this.data[index].applicant,
                respondent:this.data[index].respondent,
                currentMedaitor:this.data[index].currentMedaitor,
                mediatorid:this.data[index].mediatorid,
              })
              this.appendallocateShow = false
              this.allocateShow = false
            }
          })
          .catch((error) => {
            console.log(error)
          });
      },
      AppendAdvocate() {
        //请求当前案件调解员列表，有待修改
        this.$axios.post('https://www.wangj1106.top/DisputeWeb/manager/getMediatorList',
          {caseId:this.tempCaseId,page:this.currentPage2,size:this.currentPageSize2})
          .then((res) => {
            this.MediatorList = res.data.data.mediatorFormList,
              this.totalPages2=res.data.data.totalPages
          })
          .catch((error) => {
            console.log(error)
          });
        this.appendallocateShow = true;
      },

      submitAllocate() {
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/manager/PostMediatorForCase',
          data:{
            MediatorId:this.radio,
            CaseId:this.tempCaseId
          }
        })
          .then((res) => {
            console.log(typeof(res.data.code))
            console.log(res.data.code>=0)
            if(res.data.code>=0)
            {
              let index=this.data.findIndex(item=>{
                return item.caseId==this.tempCaseId
              })
              Vue.set(this.data,index,{
                date:this.data[index].date,
                countdown:this.data[index].countdown,
                name:this.data[index].name,
                caseId:this.data[index].caseId,
                status:2,  //调解中
                applicant:this.data[index].applicant,
                respondent:this.data[index].respondent,
                currentMedaitor:this.data[index].currentMedaitor,
                mediatorid:this.data[index].mediatorid,
              })
              // this.closeDialogAllocate()
              this.allocateShow = false
            }
          })
          .catch((error) => {
            console.log(error)
          });
      },
      closeDialogAllocate() {
        this.allocateShow = false
      },

      getFile(event) {
        this.file = event.target.files[0];
      },
      //通过申请
      submitAccept() {
        console.log(this.tempCaseId)
        // event.preventDefault();//取消默认行为
        //创建 formData 对象
        let formData = new FormData();
        // 向 formData 对象中添加文件
        if(this.file!=''){
          formData.append('file',this.file);
        }
        formData.append('caseId',this.tempCaseId)
        formData.append('id',sessionStorage.getItem("id"))
        formData.append('result',this.result)
        this.$axios({
          method: 'post',
          url: 'https://www.wangj1106.top/DisputeWeb/DisposeApply',
          data: formData,
        })
          .then((res) => {
            let nextStatus=9;
            if(this.result==1)
              nextStatus=1;
            else
              nextStatus=8;
            if(res.data.code>=0)
            {
              let index = this.data.findIndex(item => {
                return item.caseId == this.tempCaseId
              })
              console.log(this.data[index])
              Vue.set(this.data, index, {
                date: this.data[index].date,
                countdown:this.data[index].countdown,
                name: this.data[index].name,
                caseId: this.data[index].caseId,
                status: nextStatus,
                applicant: this.data[index].applicant,
                respondent: this.data[index].respondent,
                currentMedaitor:this.data[index].currentMedaitor,
                mediatorid:this.data[index].mediatorid,
              })
              // this.closeAccept()
              console.log(this.data[index])
              this.acceptApplyShow = false
            }
          })
          .catch((error) => {
            console.log(error)
          });

      },
      closeAccept() {
        this.acceptApplyShow = false
      },
      acceptApply(para) {
        this.tempCaseId = para;
        this.acceptApplyShow= true
      },

      //拒绝申请
      // submitRefuse() {
      //   // event.preventDefault();//取消默认行为
      //   //创建 formData 对象
      //   let formData = new FormData();
      //   // console.log(this.file);
      //   // 向 formData 对象中添加文件
      //   if(this.file!=''){
      //     formData.append('file',this.file);
      //   }
      //   formData.append('caseId',this.tempCaseId)
      //   formData.append('id',sessionStorage.getItem("id"))
      //   formData.append('result','0')
      //   this.$axios({
      //     method: 'post',
      //     url: 'https://www.wangj1106.top/DisputeWeb/DisposeApply',
      //     data: formData,
      //     //   {
      //     //   id: sessionStorage.getItem("id"),
      //     //   caseId: this.tempCaseId,
      //     //   result: 0,
      //     // }
      //   })
      //     .then((res) => {
      //       if(res.data.code>=0)
      //       {
      //         var index = this.data.findIndex(item => {
      //           return item.caseId = this.tempCaseId
      //         })
      //         Vue.set(this.data, index, {
      //           date: this.data[index].date,
      //           name: this.data[index].name,
      //           caseId: this.data[index].caseId,
      //           status: 8,  //立案失败
      //           applicant: this.data[index].applicant,
      //           respondent: this.data[index].respondent,
      //         })
      //         // this.closeRefuse()
      //         this.refuseApplyShow = false
      //       }
      //     })
      //     .catch((error) => {
      //       console.log(error)
      //     });
      // },
      // closeRefuse() {
      //   this.refuseApplyShow = false
      // },
      // refuseApply(para) {
      //   this.tempCaseId = para;
      //   this.refuseApplyShow = true
      // },
      //指导意见
      submitAdivise() {
        this.$axios({
          method: 'post',
          url: 'https://www.wangj1106.top/DisputeWeb/manager/advise',
          data: {
            id: sessionStorage.getItem("id"),
            caseId: this.tempCaseId,
            advise: this.advise
          }
        })
      },
      closeAdvise() {
        this.adviseShow = false
      },
      adviseTodo() {
        this.adviseShow = true
      },
      //分页2
      PreviousPage2(){
        if(this.currentPage2>1) {
          this.currentPage2 = this.currentPage2 - 1;
          this.currentPageContent2();
        }
      },
      NextPage2(){
        if(this.currentPage2<this.totalPages2){
          this.currentPage2=this.currentPage2+1;
          this.currentPageContent2();
        }
      },
      changePageSize2(){
        this.currentPage2=1;
        this.currentPageContent2();
      },
      //显示当前页内容
      currentPageContent2(){
        if(this.currentPage2<=0)
        {
          alert("页数必须大于0")
          this.currentPage2=1;
        }
        //请求案件对应可选调解员
        this.$axios.post('https://www.wangj1106.top/DisputeWeb/manager/getMediatorList',
          {caseId:this.tempCaseId,page:this.currentPage2,size:this.currentPageSize2})
          .then((res) => {
            this.MediatorList = res.data.data.mediatorFormList,
              this.totalPages2=res.data.data.totalPages
          })
          .catch((error) => {
            console.log(error)
          });
      },
    },

  }
</script>

<style scoped>
  #content {
    padding-left: 8px;
    padding-right: 8px;
  }
  .table{
    margin-top: 10px;
  }
  .tablehead{
    font-size: 18px;
  }
  .tablebody{
    font-size: 16px;
  }
  button{
    border-radius: 5px;
    outline: none;
    border: none;
  }
  .detail{
    width: 100%;
    border-radius: 5px;
    border-style: ridge;
    background-color:rgba(0,0,0,0.02);
  }
  a{
    cursor: pointer;
  }
</style>



