<template>
  <div class="container" style="width: 100%">
    <div class="row">
      <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
        <div style="height: 700px">
          <div id=message>
            <div id="daohang">
              <a href="consult.vue"><strong>咨询服务</strong></a>
              <label> >人工咨询</label>
            </div>


            <!--<textarea name="" id="" cols="150" rows="10" disabled style="border: none" placeholder="问题示例">-->
            <p>Donec tis malesuada. Praesent congue blandit metus non blandit. Vivamus ante nisl, egestas quis ultricies eget,
              sollicitudin nec mauris. Donec molestie est dui, ac tincidunt tellus placerat ac. Nulla quam turpis, tincidunt
              non lorem at, varius hendrerit diam. Ut neque erat, elementum ac molestie in, mattis non massa. Vivamus lorem
              sapien, posuere vel justo vel, condimentum tempus libero. Sed vulputate orci magna, sed aliquam ex lacinia in.
              Nullam quis tortor velit. Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse erat augue,
              convallis lacinia erat sed, ullamcorper imperdiet lectus. Donec dapibus, leo nec lobortis accumsan, lacus est
              suscipit nibh, sit amet convallis erat est et arcu.
            </p>
            <p>咨询说明：</p>
            <p>咨询对象：调解员、专家、律师</p>
            <p>咨询方式：文字、电话、语音</p>
            <p>注意:专家咨询需要预约</p>
            <!--</textarea>-->
          </div>
          <div class="radio">
            <label>
              选择咨询对象：
            </label>
            <label for="调解员">
              <input type="radio" id="调解员" name="option" value="mediator" v-model="picked" checked>调解员
            </label>
            <label for="律师">
              <input type="radio" id="律师" name="option" value="lawyer" v-model="picked">律师
            </label>
            <label for="专家">
              <input type="radio" id="专家" name="option" value="expert" v-model="picked">专家
            </label>
            <br>
            <button @click="onclick">开始咨询</button>
          </div>
        </div>
      </div>
      <div class="col-lg-9 col-md-9 col-sm-9 col-xs-9">
        <iframe src="http://127.0.0.1:8182/webdemo/im/main.html" frameborder="0" style="height: 850px;width: 100%;margin-top: 50px">
        </iframe>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    name: "person-consult",
    data() {
      return {
        picked: ""
      }
    },
    methods: {
      onclick() {
        if (this.picked === "mediator")
          this.$router.push({path: '/mediatorConsult'});
        else if (this.picked === "expert")
          this.$router.push({path:'/mediatorConsult'});
        else
          window.location.href = 'http://www.ilvdo.com/';
        }
      }
  }
</script>

<style scoped>
  .radio {
    text-align: center;
    margin-top: 100px;

  }

  #message {
    /*text-align: center;*/
    margin-top: 50px;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
  }

  #daohang {
    font-size: 20px;
    margin-bottom: 20px;
  }

  button {
    margin-top: 8px;
    background: #4c8f37;
  }
</style>



