<template>
  <div class="container">
    <div class="row">
      <!--搜索过滤区-->
      <div class="col-lg-10 col-md-10 col-sm-10 col-xs-10">
        <slot name="filter-field"></slot>
      </div>
      <!--右按钮区-->
      <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2 right" >
        <slot name="right-field"></slot>
      </div>
    </div>
    <!--表格区-->
    <slot name="table"></slot>
    <!--分页区-->
    <slot name="pagination"></slot>
  </div>
</template>

<script>
  export default {
    name: "view-page"
  }
</script>

<style scoped>
  .container{
    width: 100%;
  }
  .right button{
    float: right;
    margin-left: 5px;
  }
</style>



