<template>
  <div id="progress">
    <component :is="com"></component>
  </div>
</template>

<script>
  import UserRoleProcessPage from '../../components/usecenter/UserRoleProcess'
  import OtherRolesProcessPage from '../../components/usecenter/OtherRolesProcess'
  export default {
    components:{
      UserRoleProcessPage,
      OtherRolesProcessPage
    },
    data(){
      return{
        role:sessionStorage.getItem("role"),
      }
    },
    computed:{
      com(){
        if(this.role=='0'){
          return UserRoleProcessPage
        }
        else {
          return OtherRolesProcessPage
        }
      }
    }
  }
</script>

<style scoped>
  #progress{
    margin-bottom: 20px;
  }
</style>



