<template>
  <div>
    <div class="info">
      <ul>
        <router-link to="firstPage" tag="li" active-class="active">简要情况|</router-link>
        <router-link to="secondPage" tag="li" active-class="active">基本信息|</router-link>
        <router-link to="thirdPage" tag="li" active-class="active">纠纷概况|</router-link>
        <router-link to="fourthPage" tag="li" active-class="active">医疗行为</router-link>
      </ul>
      <div class="container">
        <div class="row">
          <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2">
            <label class="lable2">地区</label>
          </div>
          <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
            <select v-model="City">
              <option class="item" value=""><label>请选择</label></option>
              <option v-for="item in SecondPageInfo.City">{{item}}</option>
            </select>
          </div>
          <div class="col-lg-1 col-md-1 col-sm-1 col-xs-1">
            <label class="lable1">市</label>
          </div>
          <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
            <select  v-model="Zone">
              <option class="item" value=""><label>请选择</label></option>
              <option v-for="item in SelectZone(City)">{{item}}</option>
            </select>
          </div>
          <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2">
            <label class="lable1">县/区</label>
          </div>
        </div>
        <br>
        <br>

        <div class="row">
          <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2">
            <label class="lable2">医院</label>
          </div>
          <div class="col-lg-8 col-md-8 col-sm-8 col-xs-8">
            <select v-model="Hospital">
              <option class="item" value=""><label>请选择</label></option>
              <option v-for="item in SelectHopital(Zone)">{{item}}</option>
            </select>
          </div>
        </div>
        <br/>
        <br/>
        <div class="row">
          <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2">
            <label class="lable2">科室</label>
          </div>
          <div class="col-lg-8 col-md-8 col-sm-8 col-xs-8">
            <select  v-model="Department">
              <option class="item" value=""><label>请选择</label></option>
              <option v-for="item in SecondPageInfo.Department">{{item}}</option>
            </select>
          </div>
        </div>
      </div>

      <div class="conform">
        <button class="button" @click="NextStep">下一步</button>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    name: "second",
    data() {
      return {
        SecondPageInfo: {
          City: [],
          Zone: [],
          Hospital: [],
          Department: []
        },
        City: '',
        Zone: '',
        Hospital: '',
        Department: '',
        index1: '',
        index2: '',
      }
    },
    methods: {
      SelectZone(para) {
        this.index1 = this.SecondPageInfo.City.indexOf(para);
        return this.SecondPageInfo.Zone[this.index1];
      },
      SelectHopital(para) {
        if (this.index1 != -1) {
          this.index2 = this.SecondPageInfo.Zone[this.index1].indexOf(para);
          return this.SecondPageInfo.Hospital[this.index1][this.index2]
        }
      },
      NextStep() {
        this.$router.push({path: 'thirdPage'});

        var params = {
          City: this.City,
          Zone: this.Zone,
          Hospital: this.Hospital,
          Department: this.Department,
          id:sessionStorage.getItem("id")
        };
        this.$axios({
          method: 'post',
          url: 'api/postSecondPageChoose',
          data: params
        })
      }
    },
    mounted() {
      this.$axios({
        method: 'get',
        url: 'api/getSecondPageInfo',
      })
        .then((res) => {
          this.SecondPageInfo = res.data.SecondPageInfo
        })
        .catch((error) => {
          console.log(error)
        });
    },
  }
</script>

<style scoped>
  .info {
    height: 600px;
    width: 100%;
  }

  ul {
    font-size: 16px;
    text-align: center;
    margin-top: 30px;
    /*border-bottom: 1px solid grey;*/
  }

  li {
    list-style-type: none;
    display: inline;
    padding-left: 0px;
    cursor: pointer;
  }

  .info li.active,
  .info li:hover {
    background: #4781a4;
    color: #fffaf3;
    border-radius: 2px;
  }

  .lable1 {
    font-size: 18px;
    height: 100%;
    width: 100%;
    text-align: left;
    line-height: 30px;
    font-weight: lighter;
  }

  .lable2 {
    font-size: 18px;
    height: 100%;
    width: 50%;
    font-weight: lighter;
    /*border: black 1px solid;*/
  }
  .container {
    margin-top: 120px;
    width: 100%;
  }

  select {
    font-size: 18px;
    background-color: transparent;
    border-radius: 10px;
    height: 30px;
    width: 100%;
  }

  .conform {
    margin-top: 120px;
  }

  .button {
    display: inline-block;
    font-size: 18px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #3a8dbe;
    border: none;
    border-radius: 15px;
    box-shadow: 0 4px #999;
    /*bottom: 0px;*/
    /*position: absolute;*/
  }
</style>



