import Mock from 'mockjs'
Mock.mock(/login/, (req) => {
  var body = JSON.parse(req.body)
  var name = body.phone;
  var password = body.password;
  var code, msg,id,role;
  if (name == '15895920663' && password == '111111' ) {
    code = 0;
    msg = '登陆成功';
    id='00000000000';
    role = '0'
  }
  //无权限
  else if (name == '11111111111' && password == '11111111111' ) {
    code = 0;
    msg = '登陆成功';
    id='11111111111';
    role = '1'
  }
  //有权限（仅模拟两种 权限都有的调解员）
  else if (name == '15895923019' && password == '111111') {
    code = 0;
    msg = '登陆成功';
    id='22222222222';
    role = '1'
  }

  else if (name == '15895923029' && password == '111111' ) {
    code = 0;
    msg = '登陆成功';
    id='33333333333';
    role= '2'
  }
  // else if (name == '44444444444' && password == '44444444444'  ) {
  //   code = 0;
  //   msg = '登陆成功';
  //   id='44444444444';
  //   role = '3'
  // }
  else {
    code = 1;
    msg = '登陆失败';  //或更多状态：未注册
  }
  return {
    'code': code,
    'msg': msg,
    'data':{
      'id':id,
      'role':role,
    }
  }
})
