//获取调解员权限
Mock.mock(/getAuthority/, (req) => {
  var body = JSON.parse(req.body)
  var id = body.id;
  var authorityConfirm,authorityJudiciary;
  console.log(id);
  //根据调解员id返回权限
  if (id == '22222222222') {
    authorityConfirm = 1;
    authorityJudiciary = 1;
  }
  else{
    authorityConfirm = 0;
    authorityJudiciary = 0;
  }
  return {
    data: {
      'authorityConfirm': authorityConfirm,
      'authorityJudiciary': authorityJudiciary,
    }
  }
})


