<template>
  <div>
    <div class="upload">
      <button class="btn btn-primary">视频调解</button>
      <form @submit="submit">
        <div class="field">
          <label>音频文件上传</label>
          <input name="file" type="file" multiple @change="getFile($event)">
        </div>

        <div class="field">
          <label>调解记录上传</label>
          <input name="file2" type="file" multiple @change="getFile2($event)">
        </div>
        <!--<button class="btn btn-primary" type="submit" value="提交" >提交</button>-->
      </form>

      <div class="Uploaded" v-if="fileFlag==true">
        <label>已上传文件</label>
        <label v-for="item in currentStageContent.particopateContact.currentFiles">
          <a href="item.url"></a>
        </label>
      </div>

      <div class="submit">
        <button class="btn btn-primary" @click="submit" value="提交" >提交</button>
      </div>

    </div>
    <div class="foot">
      <button class="btn btn-primary"  @click="previous">上一步</button>
      <button class="btn btn-primary" @click="success">调解成功</button>
      <button class="btn btn-primary" @click="reMediation">再次调解</button>
      <button class="btn btn-primary" @click="failure">调解失败</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "to-mediating",
    props:["caseId","currentStageContent"],
    data(){
      return{
        file:'',
        file2:'',
        fileFlag:false,
      }
    },
    methods: {
      getFile(event) {
        this.file = event.target.files[0];
        console.log(this.file);
      },
      getFile2(event) {
        this.file2 = event.target.files[0];
        console.log(this.file2);
      },
      submit() {
        // event.preventDefault();//取消默认行为
        //创建 formData 对象
        let formData = new FormData();
        console.log(this.file);
        // 向 formData 对象中添加文件
        if(this.file!=''){
          formData.append('files',this.file);
        }
        if(this.file2!=''){
          formData.append('files',this.file2);
        }
        formData.append('caseId',this.caseId)
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/Mediation/MediationUpload',
          // url:'https://www.wangj1106.top/DisputeWeb/test',
          data:formData,
        })
        alert('提交成功，请选择调解结果')
      },
      success(){
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/mediator/MediationSuccess',
          data:{caseId:this.caseId},

        })
        alert('提交成功')
      }  ,
      reMediation(){
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/mediator/reMediation',
          data:{caseId:this.caseId}
        })
        alert('提交成功')
        this.$emit('previous', 1)
      },
      failure(){
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/mediator/MediationFailure',
          data:{caseId:this.caseId}
        })
        alert('提交成功')
      },
      previous(){
        this.$emit('previous', 2)
      },
    },
    mounted(){
      if(this.currentStageContent.particopateContact.currentFiles!=undefined)
        if(this.currentStageContent.particopateContact.currentFiles.length>0){
          this.fileFlag=true;
        }
    }
  }
</script>

<style scoped>
  .upload{
    height:550px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 3%;
    padding-top: 2%;
    background-color:rgba(0,0,0,0.05);
    border-radius:5px ;
  }
  .field{
    margin-top: 20px;
    /*text-align: center;*/
    margin-bottom: 50px;
  }
  label{
    font-size: 17px;
    color: #3a8dbe;
  }
  .foot {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    text-align: center;
    position: absolute;
    bottom: 15px;
  }
  .foot button{
    margin-left: 20px;
    margin-right: 20px;
  }
  button {
    font-size: 16px;
    border-radius: 5px;
  }

  .submit {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    /*position: absolute;*/
    /*bottom: 30px;*/
  }
  .submit button{
    width: 80px;
  }
</style>



