<template>
  <div>
    <h2 class="title">{{caseName}}</h2>
    <div class="content">
      <div v-if="CaseKind==='裁判文书'" v-for="item in CaseContent1">
          {{item}}
      </div>
      <div v-else-if="CaseKind==='典型案例'" v-for="item in CaseContent2">
          {{item}}
      </div>
      <div v-else="CaseKind==='纠纷调解'" v-for="item in CaseContent3">
          {{item}}
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "DisplayCase",
    data() {
      return {
        CaseKind: this.$router.params.caseKind,
        CaseName: this.$router.params.caseName,
        CaseContent1: {
          DisputeName: '',
          registerDate: '',
          DisputeType: '',
          DisputeLocation: '',
          Institution: '',
          StaffName: '',
          Abstract: '',
          DisputeResolution: '',
          comment: '',
          RelateLaw: '',
          DisputeTag: '',
        },
        CaseContent2: {
          DisputeName: '',
          registerDate: '',
          DisputeType: '',
          CourtThink: '',
          JudgeDecision: '',
          DisputeTag: '',
          personName: '',
          hospitalName: ''
        },
        CaseContent3: {
          disputeNO: '',
          district: '',
          disputeName: '',
          evaluation: '',
          fierceDegree: '',
          disputePerson: '',
          compensation: '',
          disputeDegree: '',
          acceptDate: '',
          disputeOrigin: '',
          disputeType: '',
          disputeArea: '',
          disputeLocation: '',
          Abstract: '',
          disputeResolution: '',
          resolutionDate: '',
          institution: '',
          staffName: '',
          agreement: '',
          disputeTag: '',
        }
      }
    },
    methods: {},
    mounted() {
      switch (this.caseKind) {
        case '裁判文书':
          this.$axios({
            method:'get',
            url:'https://www.wangj1106.top/search/caseDetails',
            data:{
              caseId:sessionStorage.getItem("caseId"),
              caseKind:this.CaseKind
            }
          })
            .then((res)=>{
              this.CaseContent1=res.data;
            });
          break;
        case '典型案例':
          this.$axios({
            method:'get',
            url:'https://www.wangj1106.top/search/caseDetails',
            data:{
              caseId:sessionStorage.getItem("caseId"),
              caseKind:this.CaseKind
            }
          })
            .then((res)=>{
              this.CaseContent2=res.data;
            });
          break;
        case '纠纷调解':
          this.$axios({
            method:'get',
            url:'https://www.wangj1106.top/search/caseDetails',
            data:{
              caseId:sessionStorage.getItem("caseId"),
              caseKind:this.CaseKind
            }
          })
            .then((res)=>{
              this.CaseContent3=res.data;
            });
          break;
      }
    }
  }
</script>

<style scoped>
.title{
  text-align: center;
}
  .content{
    border: 1px solid grey;
    margin: 50px auto;
    width: 60%;
  }
</style>



