<template>
  <div>
    <div class="dialog-wrap">
      <div class="dialog-cover"  v-if="isShow" @click="closeMyself"></div>
      <transition name="drop">
        <div class="dialog-content"  v-if="isShow">
          <label>{{title}}</label>
          <p class="dialog-close" @click="closeMyself">x</p>
          <!-- 插槽区 -->
          <slot>empty</slot>
          <!-- 按钮区 -->
          <div class="bottom">
            <button class="btn btn-success" @click="close">关闭</button>
            <button class="btn btn-success" @click="submit" v-if="title!='问询内容'">提交</button>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
    export default {
      props: {
        isShow: {
          type: Boolean,
          default: false
        },
        title: {
          type: String,
          default: '对话框'
        }
      },
      data() {
        return {
        }
      },
      methods: {
        closeMyself () {
          this.$emit('close')
        },
        close() {
          this.$emit('close')
        },
        submit() {
          this.$emit('submit')
        }
      }
    }
</script>transform: translateY(-500px);

<style scoped>
  label {
    font-size: 20px;
    color: #3a8dbe;
  }

  .bottom button{
    margin-left: 10px;
  }
  .drop-enter-active {
    transition: all .5s ease;
  }
  .drop-leave-active {
    transition: all .3s ease;
  }
  .drop-enter {
    transform: translateY(-500px);
  }
  .drop-leave-active {
    transform: translateY(-500px);
  }

  .dialog-wrap {
    position: fixed;
    width: 120%;
    /*height: 500%;*/
  }
  .dialog-cover {
    background: #000;
    opacity: .3;
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .dialog-content {
    width: 50%;
    position: fixed;
    /*max-height: 50%;*/
    /*overflow: auto;*/
    background: #fff;
    top: 10%;
    left: 50%;
    margin-left: -25%;
    z-index: 10;
    border: 1px solid #9c9c9c;
    border-radius: 10px;
    padding: 2%;
    line-height: 1.6;
  }
  .dialog-close {
    position: absolute;
    right: 5px;
    top: 5px;
    width: 20px;
    height: 20px;
    text-align: center;
    cursor: pointer;
  }
  .dialog-close:hover {
    color: #4fc08d;
  }
  button{
    float: right;
    border-radius: 5px;
  }
</style>



