<template>
  <div class="index">

    <!--<div class="loginTop row">-->
    <!--<ul class="list-unstyled" v-model="role">-->
    <!--<li v-for="(item,index) in flag" @click="change(index)" :class="{Cclass:flag[index]}">{{list[index]}}</li>-->
    <!--</ul>-->
    <!--</div>-->

    <div class="loginCenter container">
      <div class="row" style="width: 100%;margin-left: auto;margin-right: auto">
        <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2"></div>
        <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
          <label style="width: 80px">用户名</label>
        </div>
        <div class="col-lg-4 col-md-4 col-sm-4 col-xs-4">
          <input type="text" v-model="phone" placeholder="11位手机号">
        </div>
        <span class="form-error">{{ userErrors.errorText }}</span>
        <!--<div class="col-lg-4 col-md-4 col-sm-4 col-xs-4"></div>-->
      </div>
      <br/>
      <div class="row" style="width: 100%;margin-left: auto;margin-right: auto">
        <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2"></div>
        <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
          <label style="width: 80px">密 码</label>
        </div>
        <div class="col-lg-4 col-md-4 col-sm-4 col-xs-4">
          <input type="password" v-model="password" placeholder="6-16位字母或数字">
        </div>
        <span class="form-error">{{ passwordErrors.errorText }}</span>
        <!--<div class="col-lg-4 col-md-4 col-sm-4 col-xs-4"></div>-->
      </div>
    </div>

    <div class="loginFoot">
      <span><label @click="onReg" style="float: left;cursor:pointer;color:#3a8dbe;margin-right: 8px">注册</label></span>
      <span><label @click="anonymoity" style="float: left;cursor:pointer;color:#3a8dbe">匿名登录</label></span>
      <label @click="updatePassword" style="float: right;color: #979797;cursor:pointer">忘记密码？</label>
      <br><br>
      <span><button @click="login" @keydown.enter="login">登录</button></span>
    </div>
    <label style="color: red; font-family: 微软雅黑;font-size: 14px">{{ errorText }}</label>
  </div>
</template>

<script>
  import Vue from 'vue'

  export default {
    data() {
      return {
        role: '',
        phone: '',
        password: '',
        errorText: '',
        id: '',
        isLogin:'',
        flag: [false, false, false, false, false, false, false, false, false],
        list: [
          "普通用户",
          "调解员",
          "管理者",
          "院方人员",
          "专家",
          "法院",
          "司法厅"
        ],
        // authorityConfirm:false,
        // authorityJudiciary:false,
      }
    },

    computed: {
      userErrors() {
        let errorText, status
        if (!/^\d{11}$/g.test(this.phone)) {
          status = false
          errorText = '请输入正确的手机号'
        }
        else {
          status = true
          errorText = ''
        }
        if (!this.userFlag) {
          errorText = ''
          this.userFlag = true
        }
        return {
          status,
          errorText
        }
      },
      passwordErrors() {
        let errorText, status
        if (!/^\w{6,16}$/g.test(this.password)) {
          status = false
          errorText = '密码不是6-16位'
        }
        else {
          status = true
          errorText = ''
        }
        if (!this.passwordFlag) {
          errorText = ''
          this.passwordFlag = true
        }
        return {
          status,
          errorText
        }
      }
    },

    methods: {
      onReg() {
        this.$router.push({path: '/register'});
      },
      change(e) {
        var i = 0;
        for (; i < this.flag.length; i++) {
          if (i == e) {
            Vue.set(this.flag, e, 'true');
          }
          else {
            this.flag[i] = false;
          }
        }
        this.role = this.list[e]
      },
      login() {
        if (!this.userErrors.status || !this.passwordErrors.status) {
          this.errorText = '部分选项未通过'
        }
        // else if(this.role==undefined ||this.role=='')
        //   alert("请选择用户角色！")
        else {
          // let data = {phone: this.phone, password: this.password};
          // this.$axios.post('https://www.wangj1106.top/user/login', data)

          this.$axios({
            method: 'post',
            url:'https://www.wangj1106.top/user/login ',
            headers : {
            //   // 'Content-Type':'application/x-www-form-urlencoded'  //formData
            //   // 'Content-Type': 'multipart/form-data'  //payload
            // 'Content-Type':'application/json;charset=utf-8',
          },
            data:{
              phone: this.phone, password: this.password
            }
          })
            .then((res) => {
              if (res.data.code == 0) {
                // console.log(res.data)
                this.id = res.data.data.id;
                this.role = res.data.data.role;
                this.isLogin=true;
                sessionStorage.setItem("phone", this.phone);
                sessionStorage.setItem("role", this.role);
                sessionStorage.setItem("id", this.id);
                sessionStorage.setItem("isLogin", this.isLogin);
                // console.log(res.data.data.role)
                if (this.role == "0") {
                  this.$router.push({path: '/user'});
                }
                else if (this.role == "1") {
                  this.$router.push({path: '/mediation'});
                }
                else if (this.role == "2") {
                  this.$router.push({path: '/manager'});
                }
                else {
                  this.$router.push({path: '/userCenter/userProcess'})
                }
              }
              this.errorText = res.data.msg;
            })
            .catch((error) => {
              console.log(error)
            })
        }
      },
      anonymoity() {
        this.$router.push({path: '/consult'});
      },
      updatePassword(){
        this.$router.push({path: '/updatePassword'});
      }
    }
  }
</script>

<style scoped>
  .index {
    width: 580px;
    padding: 5px;
    border-style: ridge;
    border-radius: 10px;
    margin: 7% auto 20%;
    text-align: center;
  }

  .loginTop {
    margin: 18px auto;
    text-align: center
  }

  .loginCenter {
    width: 100%;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    font-size: 16px;
    font-family: 幼圆;
    font-weight: bolder;
  }

  input {
    background: none;
    width: 160px;
    border-radius: 5px;
  }

  .Cclass {
    color: #ffffff;
    background: #3a8dbe;
    border-radius: 5px;
  }

  .loginTop ul li {
    /*font-size: 20px;*/
    font-family: 黑体;
    text-align: center;
    margin-right: 5px;
    list-style: none;
    cursor: pointer;
    /*width: 60px;*/
  }

  label {
    margin-right: 5px;
  }

  ul {
    display: table;
    margin: 30px auto 50px;
    font-size: 16px;
    font-weight: bolder;
  }

  ul li {
    float: left;
    *display: inline;
    cursor: pointer;
  }

  .loginTop ul li.active,
  .loginTop ul li:hover {
    background: #3a8dbe;
    color: #ffffff;
    border-radius: 5px;
  }

  .loginFoot {
    text-align: center;
    width: 280px;
    margin: 40px auto 10px;
  }

  .loginFoot button {
    /*background: none;*/
    background-color: #3a8dbe;
    height: 30px;
    width: 270px;
    color: white;
    font-size: 16px;
    margin-bottom: 20px;
    /*width:80px;*/
    margin-right: 10px;
    border-radius: 5px;
  }

  .form-error {
    color: red;
    padding-left: 5px;
    font-size: 14px;
    /*font-weight: lighter;*/
    font-family: 微软雅黑;
  }
</style>



