      }
    },
    mounted() {
      if (this.role == '0')
        this.ShowUserView = true;
      else if (this.role === null) {
      }
    },
    methods: {
      getFile(event) {
        this.file = event.target.files[0];
      },
      upload(para) {
        this.uploadShow = true;
        this.currentCaseId=para;
      },
      submit1() {
        // event.preventDefault();//取消默认行为
        //创建 formData 对象
        let formData = new FormData();
        // console.log(this.file);
        // 向 formData 对象中添加文件
        if(this.file!=''){
          formData.append('file',this.file);
        }
        formData.append('id',sessionStorage.getItem("id"))
        formData.append('caseId',this.currentCaseId)
        formData.append('fileMessage',this.fileMeassage)
        this.$axios({
          method: 'post',
          url: 'https://www.wangj1106.top/DisputeWeb/user/uploadFile',
          data: formData,
        })
        this.closeEditDialog()
      },
      closeEditDialog1() {
        this.uploadShow = false;
      },
      Repealing(para,index,CaseId) {
        this.currentCaseId=CaseId;
        console.log(this.currentCaseId)
        this.ChangeEvent(index,para,CaseId);
      },
      closeEditDialog() {
        this.IsShowDialog = false;
        this.operationChosed=''
      },
      ForCaseDetail(para) {
        let caseState = this.statuses[para.status];
        let caseName = para.name;
        let caseId = para.caseId;
        let route = this.$router.resolve({
          path: '/CaseDetailforUser',
          query: {
            prePath: this.$route.path,
            caseName: caseName,
            caseState: caseState,
            caseId: caseId
          }
        });
        window.open(route.href, '_blank');
      },
    },
  }
</script>

<style scoped>
  #progress {
    padding: 8px 8px;
  }

  .detail{
    width: 100%;
    border-radius: 5px;
    border-style: ridge;
    background-color:rgba(0,0,0,0.02);
  }
  .table{
    margin-top: 10px;
  }
  .tablehead{
    font-size: 18px;
  }
  .tablebody{
    font-size: 16px;
  }
  .label1{
    font-size: 16px;
    color: #3a8dbe;
  }
  .row{
    margin-bottom: 10px;
  }
</style>



