<template>
  <div>
    <div>
      <ul class="nav nav-pills navbar-right">
        <li>
          <a data-toggle="modal" data-target="#register" href="">
            <span class="glyphicon glyphicon-user"></span>
            <label>注册</label>
          </a>
        </li>
      </ul>
    </div>
    <!-- 注册窗口 -->
    <div id="register" class="modal fade" tabindex="-1">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-body">
            <button class="close" data-dismiss="modal">
              <span>&times;</span>
            </button>
          </div>
          <div class="modal-title">
            <h1 class="text-center">注册</h1>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <div class="form-group">
                <label for="">手机号</label>
                <span class="form-error">{{ userErrors.errorText }}</span>
                <input class="form-control" type="phone" placeholder="11位手机号" v-model="phone">
              </div>
              <div class="form-group">
                <label for="">验证码</label>
                <input class="form-control" type="phone" placeholder="请输入验证码" v-model="vertifyCode">
              </div>
              <button class="btn btn-primary" type="button">发送验证码</button>
            </div>
            <div class="form-group">
              <label for="">密码</label>
              <span class="form-error">{{ passwordErrors.errorText }}</span>
              <input class="form-control" type="password" placeholder="8-16位字母或数字" v-model="password">
            </div>
            <div class="form-group">
              <label for="">再次输入密码</label>
              <span class="form-error">{{ passwordErrors.errorText2 }}</span>
              <input class="form-control" type="password" placeholder="8-16位字母或数字" v-model="password2">
            </div>
            <div class="text-center">
              <button class="btn btn-success" type="submit" @click="register" data-dismiss="modal">提交</button>
              <button class="btn btn-primary" data-dismiss="modal">取消</button>
              <br>
              <a href="" data-toggle="modal" data-dismiss="modal" data-target="#login">已有账号？点我登录</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        phone: '',
        password: '',
        password2: '',
        vertifyCode: '',
      }
    },
    computed: {
      userErrors() {
        let errorText, status
        if (!/^\d{11}$/g.test(this.phone)) {
          status = false
          errorText = '请输入正确的手机号'
        }
        else {
          status = true
          errorText = ''
        }
        if (!this.userFlag) {
          errorText = ''
          this.userFlag = true
        }
        return {
          status,
          errorText
        }
      },
      passwordErrors() {
        let errorText, status,errorText2
        if (!/^\w{6,16}$/g.test(this.password)) {
          status = false
          errorText = '密码不是6-16位'
        }
        else {
          status = true
          errorText = ''
        }
        if (!this.passwordFlag) {
          errorText = ''
          this.passwordFlag = true
        }
        if(this.password!=this.password2)
          errorText2='两次密码不一致'
        else
          errorText2=''
        return {
          status,
          errorText,
          errorText2
        }
      },
    },
    methods: {
      register() {
        if (this.phone == '')
          alert("请填写手机号")
        else if (this.vertifyCode == '') {
          alert("请输入验证码");
        }
        else if (this.password == '') {
          alert("请输入密码");
        }
        else if (this.password == this.password2 && this.password != '') {
          let data = {phone: this.phone, password: this.password};
          this.$axios.post('https://www.wangj1106.top/user/register', data)
            .then((res) => {
              if (res.data.code == 0) {
                // sessionStorage.setItem("phone", this.phone)
                // sessionStorage.setItem("role", '0')  //普通用户
                // sessionStorage.setItem("id", res.data.data.id)
                alert("注册成功！");
                location.reload();
              }
              else {
                this.errorText = res.data.msg;
              }
            })
            .catch((error) => {
              console.log(error)
            })
        }
        else {
          this.passwordErrors().errorText2 = '两次密码不一致';
        }

        // $('#register').modal('hide')
      },
  }


  }
</script>

<style scoped>
  #register{
    font-size: 16px;
  }
  .form-error {
    color: red;
    padding-left: 15px;
  }
  a{
    color: black;
    cursor: pointer;
  }
  label{
    font-size: 16px;
    /*font-weight: lighter;*/
  }
  input{
    font-size: 16px;
  }
</style>



