<template>
  <div id="app">
    <div class="app-top container">
      <div class="row">
        <!--<div class="col-lg-1 col-md-1 col-sm-1 col-xs-1"></div>-->
        <div class="col-lg-10 col-md-10 col-sm-10 col-xs-10 label-top">
          <strong><label v-text="txt" @click="backToFirst" style="cursor: pointer"></label></strong>
          <!--<div style="text-align: center;font-size: 18px" >-->
          <strong style="margin-left:25%;margin-right: 10%;font-size: 16px" v-if="!index">时间：{{date}}</strong>
          <strong style="font-size: 18px" v-if="!index">地点：{{address}}</strong>
          <!--</div>-->
        </div>
        <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2 login-top" v-if="!index">
          <span v-if="isLogin">
            <label  style="font-size: 15px">{{phone}}</label>
          <button class="btn btn-primary" v-if="isLogin" @click="logout">注销</button>
          </span>
          <span v-else>
            <login-form  @login="login"></login-form>
          <register-form></register-form>
          </span>
        </div>
      </div>


    </div>

    <div class="app-content">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>

    <div class="app-foot">
      <p>鼓楼区医患调解中心</p>
    </div>

  </div>
</template>

<script>
  import LoginForm from '../components/loginForm'
  import RegisterForm from '../components/registerForm'

  export default {
    components: {
      LoginForm,RegisterForm
    },
    data() {
      return {
        isShowLogDialog: false,
        isShowRegDialog: false,
        role:sessionStorage.getItem("role"),
        phone:sessionStorage.getItem("phone"),
        id:sessionStorage.getItem("id"),
        isLogin:sessionStorage.getItem("isLogin"),
        // LoginIsShow:true,
        txt: "医患调解中心",
        index:false,
        date: '',
        address: '鼓楼区医患纠纷调解委员会',
      }
    },
    mounted() {
      var path = this.$route.path;
      if (path.indexOf('/RegConflictPage') >= 0)
        this.txt = "纠纷登记";
      else
        this.txt="医患调解中心"
      // if( path.indexOf('/CaseDetailPage')>=0)
      //   this.txt='';
      if (path.indexOf('/index') >= 0)
        this.index = true;
      // if(this.phone==undefined)
      //   this.LoginIsShow=true;
      // else
      //   this.LoginIsShow=false;
    },
    methods: {
      backToFirst() {
        if(this.txt=="医患调解中心")
          this.$router.push({path: '/'})
      },
      login(phone,data){
        this.phone=phone;
        this.isLogin=true;
        this.role=data.data.role;
        this.id=data.data.id;
        sessionStorage.setItem("phone",this.phone)
        sessionStorage.setItem("role",this.role)
        sessionStorage.setItem("id",this.id)
        sessionStorage.setItem("isLogin",this.isLogin)
        // console.log(11111111111111)
        // console.log(this.isLogin)
      },
      logout(){
        sessionStorage.clear();
        location.reload();
      }
    },
    created() {
      var _this = this;
      this.timer = setInterval(function () {
        let Month = new Date().getMonth() + 1;
        var Minute = new Date().getMinutes();
        var Second = new Date().getSeconds();
        if (parseInt(Minute) < 10) {
          Minute = '0' + Minute;
        }
        if (parseInt(Second) < 10) {
          Second = '0' + Second;
        }
        _this.date = new Date().getFullYear() + '年' + Month + '月' + new Date().getDate() + '日' + new Date().getHours() + ':' + Minute + ':' + Second
      }, 500);
    },
    beforeDestroy() {
      if (this.timer) {
        clearInterval(this.timer);
      }
    },
  }
</script>

<style>
  body {
    /*background: url("../assets/picture/back.jpg") no-repeat;*/
    /*background-size: 100% 150%;*/
  }

  .app-top {
    width: 100%;
    /*text-align: center;*/
    padding-top: 5px;
    padding-bottom: 5px;
    /*padding-left: 100px;*/
    background-color: #5ec4e2;
    /*background: none;*/
  }
  .label-top label{
    font-family:Microsoft YaHei;
    font-weight: bold;
    font-size: 45px;
    color: white;
  }
  .login-top{
    padding-right: 2%;
    padding-top: 20px;
    text-align: right;
  }
  .login-top button{

  }

  .app-content {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 50px;
  }

  .app-foot {
    text-align: center;
    height: 30px;
    width: 100%;
    position: fixed;
    bottom: 0px;
    line-height: 30px;
    background: #dfe0e4;
    clear: both;
    margin-top: 10px;
    margin-bottom: 0px;
  }

  button{
    font-size: 15px;
    cursor: pointer;
    text-align: center;
    color: #000000;
    background-color: #ffffff;
    border: none;
    border-radius: 5px;
  }
</style>



