<template>
  <div class="detail">
    <h3
      style="text-align:center;width: 100%;margin-left: auto;margin-right: auto;font-family: 'Microsoft YaHei';color: #3a8dbe">
      {{caseName}}
    </h3>

    <div>
      <label>用户登记信息</label>
      <div class="table-responsive">
        <table class="table table-responsive table-bordered">
          <thead>
          <tr>
            <th></th>
            <th v-for="(item,index) in  registerData.stage">第{{index+1}}阶段</th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>涉事人员</td>
            <td v-for="(item,index) in  registerData.stage">{{registerData.involvedPeople}}</td>
          </tr>
          <tr>
            <td>涉事医院</td>
            <td v-for="(item,index) in  registerData.stage">{{item.involvedInstitute}}</td>
          </tr>
          <tr>
            <td>可能过失行为</td>
            <td v-for="(item,index) in  registerData.stage">{{item.behaviorList}}</td>
          </tr>
          <tr>
            <td>医疗前病症</td>
            <td v-for="(item,index) in  registerData.stage">{{item.diseasesBefore}}</td>
          </tr>
          <tr>
            <td>医疗后病症</td>
            <td v-for="(item,index) in  registerData.stage">{{item.diseasesAfter}}</td>
          </tr>
          <tr>
            <td>损害结果</td>
            <td v-for="(item,index) in  registerData.stage">{{item.resultOfDamage}}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div>
      <label>相关案例推荐</label>
      <div class="table-responsive">
        <table class="table table-responsive table-bordered table-hover">
          <tbody>
          <tr v-for="item in relatedCases" style="border: none">
            <td>{{item}}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div>
      <label>案件具体进展</label>
      <div class="table-responsive"></div>
      <table class="table table-responsive table-bordered">
        <thead>
          <tr >
            <th></th>
            <th colspan="3">用户</th>
            <th colspan="3">调解员</th>
            <th colspan="3">管理者</th>
            <th colspan="3">院方</th>
          </tr>
        </thead>
        <tbody>
         <tr>
          <td></td>
          <td>用户评价</td>
          <td>资料</td>
          <td>用户操作</td>
          <td>调解员发言</td>
          <td>资料</td>
          <td>调解员操作</td>
          <td>管理者发言</td>
          <td>资料</td>
          <td>管理者操作</td>
          <td>院方联系方式</td>
          <td>院方发言</td>
          <td>资料</td>
        </tr>
         <tr>
           <td>登记</td>
           <td></td>
           <td></td>
           <td></td>
           <td>
             {{registerData.调解员主观评判}}
           </td>
           <td></td>
           <td>
             问询记录
             <!--<textarea  rows="5" cols="40" placeholder="问询医院" v-model="InquireText"></textarea>-->
             <!--<button class="btn btn-primary" @click="submitInquire">提交</button>-->
           </td>
           <td>
             <button class="btn btn-primary">通过受理</button><br/><br/>
             <button class="btn btn-primary">拒绝受理</button>
           </td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
         </tr>
         <tr>
           <td>申请中</td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
         </tr>
         <tr>
           <td>调解中</td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
         </tr>
         <tr>
           <td>结案中</td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
         </tr>
         <tr>
           <td>已结案</td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
         </tr>
        </tbody>
      </table>
    </div>

    <div class="return">
      <button class="btn btn-primary" @click="back">返回</button>
    </div>

  </div>


</template>

<script>
  export default {
    name: "CaseDetail",
    data() {
      return {
        registerData: {
          involvedPeople:['qq',],
          stage:[
            {
              involvedInstitute:'鼓楼医院',
              behaviorList:'诊断相关-',
              diseasesBefore:'dnaefcaed',
              diseasesAfter:'cdvsvf',
              resultOfDamage:'casdca'
            }
            ]},
        relatedCases:[],
        InquireText:'',

      }
    },
    methods: {
      back(){
        this.$router.push({path: this.prepath})
      },
      submitInquire(){
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/detail/InquireInstitute',
          data:{
            caseId:this.caseId,
            inquireText:this.InquireText
          }
        })

      }
    },
    mounted(){
      this.$axios({
        method:'post',
        url:'https://www.wangj1106.top/DisputeWeb/caseDetail',
        data:{
          caseId:this.caseId
        }
      })
        .then((res) => {
          this.registerData=res.data.data.registerData;

          this.relatedCases=res.data.data.relatedCases;
          ;
        })
        .catch((error) => {
          console.log(error)
        });
      console.log(this.registerData.involvedPeople)
      this.data.forEach((item)=>{
        item.registerData.involvedPeople=item.registerData.involvedPeople.join('  ');
      })
    }
  }
</script>

<style scoped>
  .detail{
    width: 98%;
    margin-left: auto;
    margin-right: auto;
  }
  label {
    margin: 10px auto;
    font-size: 18px;
    color: #3a8dbe
  }

  button {
    border-radius: 10px;
  }

  table {
    font-size: 16px;
  }

  th{
    text-align: center;
  }
  .return {
    margin: 30px auto;
    text-align: center;
  }
</style>



