<template>
  <div id="progress">
    <div class="table-responsive">
      <table class="table table-bordered table-hover">
        <thead class="tablehead">
        <tr>
          <th>案件时间</th>
          <th>案件名称</th>
          <th>案件状态</th>
          <th>操作</th>
          <th>评价</th>
          <th>上传资料</th>
          <th>详情</th>
        </tr>
        </thead>
        <tbody v-for="(item,index) in data" class="tablebody">
        <tr>
          <td>{{item.date}}</td>
          <td>{{item.name}}</td>
          <td :class="statusColors[item.status]">{{statuses[item.status]}}</td>
          <td>
            <template>
              <div v-if="statuses[item.status]=='申请中'">
                  <span>
                    <button class="btn btn-warning" @click="Repealing('撤销申请',index,item.caseId)">撤销申请</button>
                  </span>
              </div>
              <div v-else-if="statuses[item.status]=='等待分配'">
                  <span>
                    <button class="btn btn-warning" @click="Repealing('撤销调解',index,item.caseId)">撤销调解</button>
                  </span>
              </div>
              <div v-else-if="statuses[item.status]=='调解中'">
                <select v-model="operationChosed" placeholder="请选择" @change="ChangeEvent(index,operationChosed,item.caseId)">
                  <option
                    v-for="item in options"
                    :label="item"
                    :value="item" v-if="item!='用户评价'">
                  </option>
                </select>
              </div>
              <div v-else-if="statuses[item.status]=='调解失败'">
                <span> <button class="btn btn-warning" @click="ReMediation(item.caseId)">{{'申请再次调解'}}</button></span>
              </div>
            </template>
          </td>
          <td><button class="btn btn-info" @click="Repealing('用户评价',index,item.caseId)">用户评价</button></td>
          <td><button class="btn btn-info" @click="upload(item.caseId)">上传资料</button></td>
          <td><button class="btn btn-primary" @click="ForCaseDetail(item)">查看详情</button></td>
        </tr>
        </tbody>
      </table>
    </div>

    <!--弹出框-->
    <tip-dialog :is-show="IsShowDialog" :title="dialogTitle" @close="closeEditDialog" @submit="submit()" center>
      <div v-if="IsShowGroup[0]">
        <textarea class="detail" rows="5" placeholder="请简要说明撤销申请理由" v-model="reason"></textarea>
      </div>
      <div v-if="IsShowGroup[1]">
        <textarea class="detail" rows="5" placeholder="请简要说明撤销调解理由" v-model="reason"></textarea>
      </div>
      <div v-if="IsShowGroup[2]">
        <label style="font-weight: lighter">选择鉴定机构</label>
        <select v-model="appraisalCenter" placeholder="请选择" style="width: 200px;">
          <option v-for="item in appraisalCenters" :key="item" :label="item" :value="item"></option>
        </select>
        <br>
        <textarea class="detail" rows="5" placeholder="请简要陈述鉴定信息"></textarea>
        <label class="label1">上传鉴定结果</label>
        <input type="file" multiple  @change="getFile($event)">
      </div>

      <div v-if="IsShowGroup[3]">
        申请结案操作成功
      </div>

      <div v-if="IsShowGroup[4]">
        <div class="container" style="width: 95%">
          <div class="row">
            <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2" v-for=" item in MediatorList">
          <input type="radio" v-model="radio">{{item}}
            </div>
          </div>
        </div>
      </div>

      <div v-if="IsShowGroup[5]">
        <div>
          <label class="label1">调解员评价简述</label><br>
          <textarea rows="5" class="detail" placeholder="输入您的评价" v-model="mediatorEvaluation"></textarea>
          <label class="label1">其他评价简述</label><br>
          <textarea rows="5" class="detail" placeholder="输入您的评价" v-model="otherEvaluation"></textarea>
        </div>
      </div>
    </tip-dialog>

    <upload-dialog :is-show="uploadShow" title="上传资料" @close="closeEditDialog1" @submit="submit1" center>
      <br>
      <label class="label1">文件内容说明</label>
      <textarea class="detail"  rows="3" placeholder="文件内容说明" v-model="fileMeassage"></textarea>
        <input type="file" multiple  @change="getFile($event)">
    </upload-dialog>

  </div>
</template>

<script>
  import Dialog from '../Dialog'
  import Vue from 'vue'

  export default {
    components: {
      tipDialog: Dialog,
      uploadDialog: Dialog,
    },
    data() {
      return {
        reason:'',currentCaseId:'',radio:'',fileMeassage:'',mediatorEvaluation:'',otherEvaluation:'',
        appraisalCenter: '',
        id:sessionStorage.getItem("id"),
        role: sessionStorage.getItem("role"),
        ShowUserView: false,
        uploadShow:false,
        counter: '',
        MediatorList: ['张三', '李四', '刘明瑶', '吴景贤', '王五', '周珊', '温柯洁', '杨乐', '刘秀美', '陈遥', '王杰', '张杰'],
        dialogTitle: '',
        IsShowDialog: false,
        IsShowGroup: [
          false, false, false, false, false,false,
        ],
        operationChosed:'',
        data: [
          {
            caseId:1,
            date: '2013/1/2',
            name: "张三与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 0,
          },
          {
            caseId:2,
            date: '2013/1/2',
            name: "张三与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 1,
          },
          {
            caseId:3,
            date: '2013/1/2',
            name: "李四与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 2,
          },
          {
            caseId:4,
            date: '2013/1/2',
            name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 3,
          },
          {
            caseId:5,
            date: '2013/1/2',
            name: "王五与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 4,
          },
          {
            caseId:6,
            date: '2013/1/2',
            name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 0,
          },    {
            caseId:7,
            date: '2013/1/2',
            name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 6,
          },
          {
            caseId:8,
            date: '2013/1/2',
            name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 7,
          },
          {
            caseId:9,
            date: '2013/1/2',
            name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 1,
          },
          {
            caseId:10,
            date: '2013/1/2',
            name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 7,
          },
          {
            caseId:11,
            date: '2013/1/2',
            name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 8,
          },
          {
            caseId:12,
            date: '2013/1/2',
            name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 8,
          },
          {
            caseId:13,
            date: '2013/1/2',
            name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 7,
          },
          {
            caseId:14,
            date: '2013/1/2',
            name: "许译文与永州市安邦医疗器械有限责任公司医疗损害责任纠纷",
            status: 8,
          },
        ],
        statuses: ['申请中','等待分配', '调解中','调解失败', '结案中', '已结案', '撤销申请', '撤销调解','立案失败'],
        statusColors: ['warning', 'warning', 'success','danger', 'success', 'info', 'info', 'info','danger'],
        options: ['撤销申请','撤销调解', '申请鉴定', '申请结案', '更换调解员','用户评价'],
        appraisalCenters: ['鉴定中心A', '鉴定中心b', '鉴定中心c', '鉴定中心s', '鉴定中心f', '鉴定中心d',],
      }
    },
    mounted() {
      if (this.role == '0')
        this.ShowUserView = true;
      else if (this.role === null) {
      }
      // else
      //   this.ShowUserView = false;
      //   this.$axios.post('http://223.3.93.238:8080/user/getUserCaseList',{id:sessionStorage.getItem("id")})
      //   .then((res) => {
      //     this.data = res.data.data
      //   })
      //   .catch((error) => {
      //     console.log(error)
      //   });
    },
    methods: {
      getFile(event) {
        this.file = event.target.files[0];
      },
      upload(para) {
        this.uploadShow = true;
        this.currentCaseId=para;
      },
      submit1() {
        // event.preventDefault();//取消默认行为
        //创建 formData 对象
        let formData = new FormData();
        // console.log(this.file);
        // 向 formData 对象中添加文件
        if(this.file!=''){
          formData.append('file',this.file);
        }
        formData.append('id',sessionStorage.getItem("id"))
        formData.append('caseId',this.currentCaseId)
        formData.append('fileMessage',this.fileMeassage)
        this.$axios({
          method: 'post',
          url: 'http://223.3.93.238:8080/DisputeWeb/user/uploadFile',
          data: formData,
        })
        this.closeEditDialog()
      },
      closeEditDialog1() {
        this.uploadShow = false;
      },
      Repealing(para,index,CaseId) {
        this.currentCaseId=CaseId;
        console.log(this.currentCaseId)
        this.ChangeEvent(index,para,CaseId);
      },
      closeEditDialog() {
        this.IsShowDialog = false;
       this.operationChosed=''
      },
      submit() {
        if(this.dialogTitle=='撤销申请') {
          console.log(this.currentCaseId)
          this.$axios({
            method: 'post',
            url: 'http://223.3.93.238:8080/DisputeWeb/user/caseCancelApply',
            data: {
              id:this.id,
              caseId: this.currentCaseId}
          });
          let index=this.data.findIndex((item)=>{
            return item.caseId=this.currentCaseId
          });
          Vue.set(this.data,index,{
            caseId:this.currentCaseId,
            date: this.data[index].date,
            name: this.data[index].name,
            status: 6,
          })
        }
        if(this.dialogTitle=='撤销调解') {
          this.$axios({
            method: 'post',
            url: 'http://223.3.93.238:8080/DisputeWeb/user/caseCancellMediation',
            data: {
              id:this.id,
              caseId: this.currentCaseId}
          })
          let index=this.data.findIndex((item)=>{
            return item.caseId==this.currentCaseId
          })
          Vue.set(this.data,index,{
            caseId:this.currentCaseId,
            date: this.data[index].date,
            name: this.data[index].name,
            status: 7,
          })
        }
        if(this.dialogTitle=='申请结案') {
          this.$axios({
            method: 'post',
            url: 'http://223.3.93.238:8080/ DisputeWeb/user/caseSettle',
            data: {
              id:this.id,
              caseId: this.currentCaseId}
          })


          let index=this.data.findIndex((item)=>{
            return item.caseId==this.currentCaseId
          });
          Vue.set(this.data,index,{
            caseId:this.currentCaseId,
            date: this.data[index].date,
            name: this.data[index].name,
            status: 4,
          })
        }
        if(this.dialogTitle=='更换调解员') {
          this.$axios({
            method: 'post',
            url: 'http://223.3.93.238:8080/ DisputeWeb/user/changeMediator',
            data: {
              id:this.id,
              caseId: this.currentCaseId,
              mediatorId:radio,
            }
          })


          let index=this.data.findIndex((item)=>{
            return item.caseId==this.currentCaseId
          });
          Vue.set(this.data,index,{
            caseId:this.currentCaseId,
            date: this.data[index].date,
            name: this.data[index].name,
            status: 4,
          })
        }
        if(this.dialogTitle=='用户评价') {
          this.$axios({
            method: 'post',
            url: 'http://223.3.93.238:8080/ DisputeWeb/user/evaluation',
            data: {
              id:this.id,
              caseId: this.currentCaseId,
              mediatorEvaluation:this.mediatorEvaluation,
              otherEvaluation:this.otherEvaluation,
            }
          })


          let index=this.data.findIndex((item)=>{
            return item.caseId==this.currentCaseId
          });
          Vue.set(this.data,index,{
            caseId:this.currentCaseId,
            date: this.data[index].date,
            name: this.data[index].name,
            status: 4,
          })
        }
        this.closeEditDialog();
      },
      ChangeEvent(index,e,CaseId) {
        this.currentCaseId=CaseId;
        this.counter=index;
        this.IsShowDialog = true;
        let i = 0;
        this.dialogTitle=e;
        let arrayIndex = this.options.indexOf(e);
        for (; i < this.IsShowGroup.length; i++) {
          if (i == arrayIndex) {
            Vue.set(this.IsShowGroup, i, 'true');
          }
          else {
            this.IsShowGroup[i] = false;
          }
        }
      },

      ReMediation(CaseId){
        console.log(CaseId);
        let r=confirm('是否申请再次调解?');
        if(r==true){
          let index=this.data.findIndex(item=>{
            return item.caseId==CaseId
          })
          Vue.set(this.data,index,{
            caseId:CaseId,
            date: this.data[index].date,
            name:  this.data[index].name,
            status: 2,
            radio:this.data[index].radio
          })

          this.$axios({
            method:'post',
            url:'http://223.3.93.238:8080/DisputeWeb/user/reMediation',
            data:{
              id:this.id,
              CaseId:CaseId}
          })
        }
      },
      ForCaseDetail(para) {
        let caseState = this.statuses[para.status];
        let caseName = para.name;
        let caseId = para.caseId;
        let route = this.$router.resolve({
          path: '/CaseDetailforUser',
          query: {
            prePath: this.$route.path,
            caseName: caseName,
            caseState: caseState,
            caseId: caseId
          }
        });
        window.open(route.href, '_blank');
      },
    },
  }
</script>

<style scoped>
  #progress {
    padding: 8px 8px;
  }

  .detail{
    width: 100%;
    border-radius: 5px;
    border-style: ridge;
    background-color:rgba(0,0,0,0.02);
  }
  .table{
    margin-top: 10px;
  }
  .tablehead{
    font-size: 18px;
  }
  .tablebody{
    font-size: 16px;
  }
  .label1{
    font-size: 16px;
    color: #3a8dbe;
  }
  .row{
    margin-bottom: 10px;
  }
</style>



