<template>
  <div class="detail">
    <h3
      style="text-align:center;width: 100%;margin-left: auto;margin-right: auto;font-family: 'Microsoft YaHei';color: #3a8dbe">
      {{caseName}}
    </h3>

    <div>
      <label>用户登记信息</label>
      <div class="table-responsive">
        <table class="table table-responsive table-bordered">
          <thead>
          <tr>
            <th></th>
            <th v-for="(item,index) in  registerData.stage">第{{index+1}}阶段</th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>涉事人员</td>
            <td v-for="(item,index) in  registerData.stage">{{registerData.involvedPeople}}</td>
          </tr>
          <tr>
            <td>涉事医院</td>
            <td v-for="(item,index) in  registerData.stage">{{item.involvedInstitute}}</td>
          </tr>
          <tr>
            <td>可能过失行为</td>
            <td v-for="(item,index) in  registerData.stage">{{item.behaviorList}}</td>
          </tr>
          <tr>
            <td>医疗前病症</td>
            <td v-for="(item,index) in  registerData.stage">{{item.diseasesBefore}}</td>
          </tr>
          <tr>
            <td>医疗后病症</td>
            <td v-for="(item,index) in  registerData.stage">{{item.diseasesAfter}}</td>
          </tr>
          <tr>
            <td>损害结果</td>
            <td v-for="(item,index) in  registerData.stage">{{item.resultOfDamage}}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div>
      <label>相关案例推荐</label>
      <div class="table-responsive">
        <table class="table table-responsive table-bordered table-hover">
          <tbody>
          <tr v-for="item in relatedCases" style="border: none">
            <td>{{item}}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="return">
      <button class="btn btn-primary" @click="back">返回</button>
    </div>

  </div>


</template>

<script>
  export default {
    name: "CaseDetail",
    data() {
      return {
        registerData: {
          involvedPeople:['qq',],
          stage:[
            {
              involvedInstitute:'鼓楼医院',
              behaviorList:'cbnakcbdj',
              diseasesBefore:'dnaefcaed',
              diseasesAfter:'cdvsvf',
              resultOfDamage:'casdca'
            }
          ]},
        stage:["登记","申请中","调解中","结案中","已结案"],
        relatedCases:[],
        InquireText:'',

      }
    },
    methods: {
      back(){
        this.$router.push({path: this.prepath})
      },
      submitInquire(){
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/detail/InquireInstitute',
          data:{
            caseId:this.caseId,
            inquireText:this.InquireText
          }
        })

      }
    },
    mounted(){
      this.$axios({
        method:'post',
        url:'https://www.wangj1106.top/DisputeWeb/caseDetail',
        data:{
          caseId:this.caseId
        }
      })
        .then((res) => {
          this.registerData=res.data.data.registerData;
          this.relatedCases=res.data.data.relatedCases;
          ;
        })
        .catch((error) => {
          console.log(error)
        });
      this.data.forEach((item)=>{
        item.registerData.involvedPeople=item.registerData.involvedPeople.join('  ');
      })
    }
  }
</script>

<style scoped>
  .detail{
    width: 98%;
    margin-left: auto;
    margin-right: auto;
  }
  label {
    margin: 10px auto;
    font-size: 18px;
    color: #3a8dbe
  }

  button {
    border-radius: 10px;
  }

  table {
    font-size: 16px;
  }

  th{
    text-align: center;
  }
  .return {
    margin: 30px auto;
    text-align: center;
  }
</style>



