<template>
  <div>
    <div class="info">
      <label>纠纷要素预览</label>
      <div class="tableInfo">
        <table class="table  table-bordered table-hover">
          <thead>
          <tr>
            <th style="text-align:center; color: #3a8dbe;">医疗行为</th>
            <th style="text-align:center; color: #3a8dbe;">疾病</th>
            <th style="text-align:center; color: #3a8dbe;">症状</th>
            <th style="text-align:center; color: #3a8dbe;">损害后果</th>
            <th style="text-align:center; color: #3a8dbe;">排序</th>
          </tr>
          </thead>

          <tbody v-for="(item,index) in list">
          <tr style="text-align: center;font-size: 16px;font-weight: lighter;height: 30px">
            <td contentEditable="true">{{item.medicalPractice}}</td>
            <td contentEditable="true">{{item.disease}}</td>
            <td contentEditable="true">{{item.syndrome}}</td>
            <td contentEditable="true">{{item.result}}</td>
            <td style="width:240px">
              <button class="btn-primary up" @click="up(index)">↑</button>
              <button class="btn-primary down" @click="down(index)">↓</button>
              <button class="btn-primary deleteword" @click="deleteword(index)">×</button>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
      <div class="ButtonID">
        <button class="btn btn-primary" @click="AddLine()">添加</button>
        <button class="btn btn-primary" @click="DeleteLine()">删除</button>
      </div>
      <label>简要情况</label>
      <div class="brief">
        <textarea id="show" rows="6">

        </textarea>
      </div>
    </div>
    <div class="confirm">
      <button class="btn btn-primary" @click="PreviousStep">上一步</button>
      <button class="btn btn-primary" @click="NextStep">下一步</button>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'

  export default {
    name: "KeyWord",
    data() {
      return {
        list: [
        ],
      }
    },
    methods: {
      PreviousStep(){
        this.$router.push({path: 'BasicDivideInfo'});
      },
      NextStep() {
        this.$router.push({path: 'RelatedCasePage'})
        this.$axios({
          method: 'post',
          url: 'https://www.wangj1106.top/search/words',
          data: {
            caseId:sessionStorage.getItem("caseId"),
            list:this.list
          }
        })
      },
      AddLine() {
        let temp = {
          medicalPractice: '',
          disease: '',
          syndrome:'',
          result: ''
        }
        this.list.push(temp)
      },
      DeleteLine() {
        this.list.pop()
      },
      up(para) {
        if (para > 0) {
          let temp = this.list[para];
          Vue.set(this.list, para, this.list[para - 1]);
          Vue.set(this.list, para - 1, temp);
        }
      },
      down(para) {
        if (para < this.list.length - 1) {
          let temp = this.list[para];
          Vue.set(this.list, para, this.list[para + 1]);
          Vue.set(this.list, para + 1, temp);
        }
      },
      deleteword(index) {
        this.list.splice(index, 1)
      }
    },
    mounted() {
      this.$axios({
        method: 'post',
        url: 'https://www.wangj1106.top/RegConflict/getKeyWordList',
        data:{
          id:sessionStorage.getItem("id")}
      })
        .then((res) => {
          this.list = res.data.KeyWordList
        })
    }
  }
</script>

<style scoped>
  .info {
    width: 100%;
  }

  .tableInfo{
    height:300px;
    overflow: scroll;
    overflow-x:hidden;
    background-color:rgba(0,0,0,0.03);
  }
  label {
    margin: 8px auto;
    font-size: 20px;
    color: #3a8dbe;
  }
  .up,
    .down,
    .deleteword {
      margin-left: 15px;
      margin-right: 15px;
      width: 40px;
    }
  .ButtonID{
    width: 100%;
    text-align: center;
    margin-top: 20px;
    /*margin-bottom: 30px;*/
  }

  .ButtonID button {
    width: 80px;
    margin-left:30px;
    margin-right: 30px;
  }
  .confirm {
    margin-top: 20px;
    text-align: center;
  }
  .confirm button {
    width: 80px;
    margin-left:30px;
    margin-right: 30px;
  }
  button {
    font-size: 16px;
    border-radius: 5px;
  }
  #show{
    width: 100%;
    height:250px;
    overflow: scroll;
    overflow-x:hidden;
    padding: 10px 20px;
    border-radius: 10px;
    background-color:rgba(0,0,0,0.03);
    border: none;
  }
</style>



