// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Layout from './components/layout'
import './assets/css/bootstrap.min.css'
import  './assets/js/jquery-3.3.1.min'
import './assets/js/bootstrap.min'

import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import index from './pages/index'
import UserPage from './pages/userPage'
import Consult from './pages/consult/consult'
import PersonConsult from './pages/consult/PersonConsult'
import UserCenter from './pages/useCenter/userCenter'
import History from './pages/useCenter/history'
import Message from './pages/useCenter/message'
import Manager from './pages/manager/manager'
import Progress from './pages/manager/progress'
import UserProcess from './pages/useCenter/userProcess'
import MediatorConsult from './pages/consult/mediatorConsult'
import MyMediation from './pages/Mediation/myMediation/myMediation'
import MediatingPage from './pages/Mediation/myMediation/Mediating'
import JudiciaryCasePage from './pages/common/JudiciaryCase'
import Mediation from './pages/Mediation/mediation'
import RegConflictPage from './pages/RegConflict/RegConflict'
import MediationHallPage from './pages/Mediation/MediationHall'
import InvolvedInfoPage from './pages/RegConflict/InvolvedInfo'
import KeyWordPage from './pages/RegConflict/KeyWord'
import RelatedCasePage from './pages/RegConflict/RelatedCase'
import BasicDivideInfo from './pages/RegConflict/BasicDivideInfo'
import BasicDivideInfo2 from './pages/RegConflict/oLDBasicDivideInfo'
import firstPage from './pages/RegConflict/BasicInfo/first'
import secondPage from './pages/RegConflict/BasicInfo/second'
import thirdPage from './pages/RegConflict/BasicInfo/third'
import fourthPage from './pages/RegConflict/BasicInfo/fourth'
import CaseDetailPage from './pages/common/CaseDetail'
import CaseDetailforUser from './pages/common/CaseDetailforUser'
import Register from './pages/Register'
import InfoOfModeratorPage from './pages/RegConflict/InfoOfModerator'
import MederatorRegister from './pages/RegConflict/ModeratorRegister'
import authorityAllocate from './pages/manager/authorityAllocate'
import expertPage from './pages/manager/expert'
import updatePassword from './pages/UpdatePassword'
import axios from 'axios'
import Allocatee from './pages/Allocatee'
import DisplayCase from './pages/common/DisplayCase'
import './mock/mock'
import './mock/common'
import './mock/manager'
import './mock/manager'
import './mock/Judiciary'
import './mock/mediatior'
import './mock/RegConflict'
Vue.prototype.$axios = axios;

Vue.use(VueRouter)
Vue.use(VueResource)
Vue.config.productionTip = false

let router = new VueRouter({
  mode: 'history',
  routes: [
    //统一登录页
    {
      path: '/',
      redirect: '/index',
    },
    {
      path: '/index',
      component: index
    },
    //普通用户首页
    {
      path: '/user',
      component: UserPage
    },
    //咨询首页
    {
      path: '/consult',
      component: Consult
    },
    //人工咨询
    {
      path: '/personConsult',
      component: PersonConsult
    },
    //注册页
    {
      path: '/register',
      component: Register
    },
    //密码找回
    {
      path: '/updatePassword',
      component: updatePassword
    },
    //用户中心
    {
      path: '/userCenter',
      component: UserCenter,
      redirect: '/userCenter/history',
      children: [
        {
          path: 'history',
          component: History
        },
        {
          path: 'message',
          component: Message
        },
        {
          path: 'userProcess',
          component: UserProcess
        }
      ]
    },
    //纠纷登记
    {
      path: '/RegConflictPage',
      component: RegConflictPage,
      redirect: '/RegConflictPage/InvolvedInfoPage',
      children: [
        {
          path: "InvolvedInfoPage",
          component: InvolvedInfoPage
        },
        // {
        //   path: "BasicDivideInfo",
        //   component: BasicDivideInfo
        // },
        {
          path: "BasicDivideInfo",
          component: BasicDivideInfo
        },
        {
          path: "firstPage",
          component: firstPage
        },
        {
          path: "secondPage",
          component: secondPage
        },
        {
          path: "thirdPage",
          component: thirdPage
        },
        {
          path: "fourthPage",
          component: fourthPage
        },
        {
          path: "KeyWordPage",
          component: KeyWordPage
        },
        {
          path: "RelatedCasePage",
          component: RelatedCasePage
        },
        {
          path: 'InfoOfModeratorPage',
          component: InfoOfModeratorPage
        },
        {
          path: 'MederatorRegister',
          component: MederatorRegister
        }
      ]
    },
    {
      path:'/DisplayCase',
      component:DisplayCase
    },
    //调解员and管理员权限详情页
    {
      path: '/CaseDetailPage',
      component: CaseDetailPage
    },
    //用户权限详情页
    {
      path: '/CaseDetailforUser',
      component:CaseDetailforUser
    },
    //管理者页
    {
      path: '/manager',
      component: Manager,
      redirect: '/manager/progress',
      children: [
        {
          path: 'progress',
          component: Progress
        },
        {
          path: "JudiciaryCasePage",
          component: JudiciaryCasePage
        },
        {
          path: "authorityAllocate",
          component: authorityAllocate
        },
        {
          path: "expertPage",
          component: expertPage
        },
      ]
    },

    {
      path: '/mediatorConsult',
      component: MediatorConsult
    },
    //调解员页面
    {
      path: "/mediation",
      component: Mediation,
      redirect: "/mediation/MediationHallPage",
      children: [
        {
          path: "MediationHallPage",
          component: MediationHallPage,
        },
        {
          path: "JudiciaryCasePage",
          component: JudiciaryCasePage
        },
        {
          path: "MediatingPage",
          component: MediatingPage
        },
        {
          path: "myMediation",
          component: MyMediation,
        },
      ]
    },
    {
      path: '/Allocatee',
      component: Allocatee
    },
    // //具体调解页面
    // {
    //   path: "MediatingPage",
    //   component: MediatingPage
    // },
  ]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {Layout},
  template: '<Layout/>',
})



