<template>
  <div id="content">
    <view-page>
      <!--<template slot="filter-field">-->
        <!--&lt;!&ndash; 状态过滤框 &ndash;&gt;-->
        <!--<label for="" >筛选：</label>-->
        <!--<select v-model="filterStatus" placeholder="选择案件状态(全部)" style="height: 28px" @change="changeStatus">-->
          <!--<option label="选择案件状态(全部)" value="" ></option>-->
          <!--<option v-for="(status,index) in statues" :key="status" :label="status" :value="index"></option>-->
        <!--</select>-->
      <!--</template>-->
      <template slot="table">
        <div class="table-responsive">
          <table class="table table-bordered table-hover">
            <thead  class="tablehead">
            <tr>
              <th>案件名称</th>
              <th>申请书</th>
              <th>材料</th>
              <!--<th>意向专家</th>-->
              <!--<th>紧急程度</th>-->
              <!--<th>案件状态</th>-->
              <th>审批</th>
              <th>查看详情</th>
            </tr>
            </thead>
            <tbody v-for="item in data" class="tablebody">
            <tr >
              <td>{{item.caseName}}</td>
              <td><a href="item.appointExpert.application"></a>{{item.appointExpert.application.name}}</td>
              <td> <span v-for="item2 in item.appointExpert.files"><a href="item2.url">{{item2.name}}</a> <br></span> </td>
              <!--<td>{{item.expert}}</td>-->
              <!--<td>{{item.emergencyDegree}}</td>-->
              <!--<td><button class="btn btn-success" @click="expertAllocate">分配专家</button></td>-->
              <!--<td :class="caseColor[item.caseStatus]">-->
                <!--{{item.caseStatus}}-->
              <!--</td>-->
              <td>
                <button class="btn btn-success" @click="ApproveExpertAllocate(item.caseId)">同意</button>
                <button class="btn btn-success" @click="RefuseExpertAllocate(item.caseId)">拒绝</button>
              </td>
              <td><button class="btn btn-primary" @click="ForCaseDetail(item)">查看详情</button></td>
            </tr>
            </tbody>
          </table>
        </div>
      </template>
      <template slot="pagination">
        共{{caseTotalPages}}页
        <select  v-model="caseCurrentPageSize" style="margin-left: 30px" @change="CasechangePageSize">
          <option v-for="item in PageSizes" :value="item" :label="item"></option>
        </select>条/页

        <button class="btn btn-info" @click="CasePreviousPage" style="margin-left: 30px">上一页</button>
        <!--第{{currentPage}}页-->
        <button class="btn btn-info" @click="CaseNextPage"  style="margin-right: 30px">下一页</button>
        前往第
        <input type="number" style="width:50px" v-model="caseCurrentPage">页
      </template>
    </view-page>
  </div>

</template>

<script>
  import ViewPage from '../../components/ViewPage'
  import Dialog from '../../components/Dialog'
  export default {
    name: "expert",
    components: {
      ViewPage,
      ExpertDialog: Dialog
    },
    data(){
      return{
        radio:'',
        expertShow:false,
        caseCurrentPage:1,
        caseCurrentPageSize:10,
        caseTotalPages:1,
        PageSizes:[2,5,10],
        expertList:[{name:'wkj',id:23}],
        caseColor:{已分配:'success',尚未分配:'info',等待分配:'warning'},
        statues:['等待分配','尚未分配','已分配'],
        filterStatus:'',
        data: [
          {
            caseId: "1538134338806300913",
            caseName: "李双双与江苏省人民医院医疗纠纷",
            appointExpert: {
              application: {
                name: "专家分配系列api .txt",
                url: "pe93soaos.bkt.clouddn.com/1538238203699225315/专家分配系列api .txt"
              },
              files: [
                {
                  name: "在此处输入标题.pdf",
                  url: "http://pe93soaos.bkt.clouddn.com/1538238203699225315/在此处输入标题.pdf"
                },
                {
                  name: "专家分配系列api .txt",
                  url: "http://pe93soaos.bkt.clouddn.com/1538238203699225315/专家分配系列api .txt"
                }
              ]
            }
          }
        ]

      }
    },
    methods:{
      ForCaseDetail(para) {
        let caseName = para.name;
        let caseId = para.caseId;
        let route = this.$router.resolve({
          path: '/CaseDetailPage',
          query: {
            prePath: this.$route.path,
            caseName: caseName,
            caseId: caseId
          }
        });
        window.open(route.href, '_blank');
      },
      expertAllocate(){

        this.expertShow=true
      },
      closeDialogExpert(){
        this.expertShow=false
      },
      submitExpert(){
        this.$axios.post('http://223.3.83.239:8080/DisputeWeb/manager/postExpertChosed',
          {
            size:this.currentPageSize,
            page:this.currentPage,
            expert:this.radio,
          })
          .then((res) => {
          })
          .catch((error) => {
            console.log(error)
          });
      },
      //专家分配页面分页
      CasePreviousPage(){
        if(this.caseCurrentPage>1) {
          this.caseCurrentPage = this.caseCurrentPage - 1;
          this.CasecurrentPageContent();
        }
      },
      CaseNextPage(){
        if(this.caseCurrentPage<this.caseTotalPages){
          this.caseCurrentPage=this.caseCurrentPage+1;
          this.CasecurrentPageContent();
        }
      },
      CasechangePageSize(){
        this.caseCurrentPage=1;
        this.CasecurrentPageContent();
      },
      //显示当前页内容
      CasecurrentPageContent(){
        if(this.currentPage<=0)
        {
          alert("页数必须大于0")
          this.currentPage=1;
        }
        //请求案件列表
        this.$axios.post('http://223.3.83.239:8080/DisputeWeb/allocatee/getExpertManageList',
          {
            size:this.caseCurrentPageSize,
            page:this.caseCurrentPage,
            // filterStatus:this.filterStatus,
          })
          .then((res) => {
            //从后台返回的数据
            // this.data = res.data.data,
            // this.totalPages=res.data.data.totalPages
          })
          .catch((error) => {
            console.log(error)
          });

      },

      changeStatus(){
        this.currentPage=1;
        this.CasecurrentPageContent();
      },
      //同意当前分配的专家
      ApproveExpertAllocate(para){
        this.$axios({
          methods:'post',
          url:'http://223.3.83.239:8080/DisputeWeb/manager/postExpertResult',
          data:{
            caseId:para,
            result:1
          }
        })
        alert("成功通过当前的专家分配");
      },
    //  拒绝当前分配的专家
      RefuseExpertAllocate(para){
        this.$axios({
          methods:'post',
          url:'http://223.3.83.239:8080/DisputeWeb/manager/postExpertResult',
          data:{
            caseId:para,
            result:0
          }
        })
        alert("成功拒绝当前的专家分配");
      }
    },
    mounted(){
      // this.$axios({
      //   methods:'get',
      //   url:'http://223.3.83.239:8080/DisputeWeb/manager/getExpertList',
      //   data:{
      //     caseId:this.tempCaseId,
      //     size:this.currentPageSize,
      //     page:this.currentPage,
      // }
      // })
      //   .then(res=>{
      //     //取决于后端返回数据的格式
      //     // this.expertList=res.data();
      //   })
      //   .catch((error) => {
      //     console.log(error)
      //   });
    //  管理员获取专家管理页面数据
      this.$axios.post('http://223.3.83.239:8080/DisputeWeb/allocatee/getExpertManageList',
        {
          size:this.caseCurrentPageSize,
          page:this.caseCurrentPage,
          filterStatus:this.filterStatus,
        })
        .then((res) => {
          //从后台返回的数据
          // this.data = res.data.data,
          // this.totalPages=res.data.data.totalPages
        })
        .catch((error) => {
          console.log(error)
        });
    }
  }
</script>

<style scoped>
  #content {
    padding-left: 8px;
    padding-right: 8px;
  }
  .table{
    margin-top: 10px;
  }
  .tablehead{
    font-size: 18px;
  }
  .tablebody{
    font-size: 16px;
  }
  button{
    border-radius: 10px;
  }
  label{
    font-size: 15px;
    font-family: "Times New Roman";
  }
</style>



