<template>
  <div class="ListName">
    <label class="label1">调解员列表</label>
    <div class="info">
      <div class="container" style="width: 100%;margin-top:20px">
        <div class="row">
          <div class="col-md-2" v-for="item in MediatorList" style="height: 50px">
            <input type="checkbox" :id="item.id"  :value="item.id" v-model="MediatorPicked"><label :for="item.id">{{item.name}}</label>
          </div>
        </div>
      </div>
    </div>
    <!--{{MediatorPicked}}-->
    <div class="bottom">
      <button class="btn btn-primary" @click="chooseMediator">提交</button>
      <button class="btn btn-primary" @click="skip">跳过</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "InforOfModerator",
    data() {
      return {
        MediatorList: [],
        MediatorPicked: [],
        list: [],
        caseId:sessionStorage.getItem('caseId')
      }
    },
    mounted() {
      this.$axios({
        methos: 'post',
        // url: 'https://www.wangj1106.top/DisputeWeb/manager/getMediatorListWithPage',
        url: 'https://www.wangj1106.top/DisputeWeb/manager/getMediatorList',
        data:{caseId:this.caseId}
      })
        .then((res) => {
          // this.MediatorList = res.data.MediatorList;
          this.MediatorList = res.data.data;
          console.log(this.MediatorList)
          // for (let n = 0; n < res.data.MediatorList.length; n++)
          //   this.MediatorPicked.push(false);
        })
    },
    methods: {
      chooseMediator() {
        // for (let n = 0; n < this.MediatorPicked.length; n++) {
        //   if (this.MediatorPicked[n] == true)
        //     this.list.push(this.MediatorList[n].id)
        // }
        this.$axios({
          methos: 'post',
          url: 'https://www.wangj1106.top/user/postMediatorList',
          data: {
            CaseId:this.CaseId,
            MediatorPickedlist: this.list}
        });
        alert('已提交，登记已完成'+'\n'+'请及时下载代理人委托书并在用户中心上传')
      },
      skip() {
        alert('登记已完成'+'\n'+'请及时下载代理人委托书并在用户中心上传')
      }
    }
  }
</script>

<style scoped>
  .ListName {
    width: 100%;
  }
  .info{
    height:650px;
    overflow: scroll;
    overflow-x:hidden;
    padding: 10px 20px;
    border-radius: 10px;
    background-color:rgba(0,0,0,0.03);
    font-size: 17px;
  }
  radio{
    display: inline;
  }
  .label1 {
    margin: 8px auto;
    font-size: 20px;
    color: #3a8dbe;
  }

.bottom{
  margin-top: 20px;
  text-align: center;
}
  .bottom button{
    margin-left:30px;
    margin-right: 30px;
    width: 80px;
  }

  button {
    font-size: 16px;
    border-radius: 5px;
  }
</style>



