      SaveCurrentPair(para){
        let temp;
        switch (para){
          case 0:
            temp={
              DiseaseKind:this.stages[this.currentstage-1].DiseaseBefore[this.stages[this.currentstage-1].btnChosed].type,
              DiseaseName:this.stages[this.currentstage-1].DiseaseBefore[this.stages[this.currentstage-1].btnChosed].name
            };
            this.stageContent[this.currentstage-1].resultOfRegConflict[this.stages[this.currentstage-1].btnChosed].diseaseListBefore.push(temp);
            break;
          case 1:
            temp={
              DiseaseKind:this.stages[this.currentstage-1].DiseaseAfter[this.stages[this.currentstage-1].btnChosed].type,
              DiseaseName:this.stages[this.currentstage-1].DiseaseAfter[this.stages[this.currentstage-1].btnChosed].name
            };
            this.stageContent[this.currentstage-1].resultOfRegConflict[this.stages[this.currentstage-1].btnChosed].diseaseListAfter.push(temp);
            break;
        }
