      DeleteType(index) {
        this.stages[this.currentstage - 1].TypeControl[this.stages[this.currentstage - 1].btnChosed][index].value = false;
      },
      AppendType(para) {
        let index;
        index = this.stages[this.currentstage - 1].TypeControl[this.stages[this.currentstage - 1].btnChosed].findIndex((item) => {
          return item.cname == para
        });
        this.stages[this.currentstage - 1].TypeControl[this.stages[this.currentstage - 1].btnChosed][index].value = true;
      },

      //涉事医院展开控制
      Toggle(para) {
        switch (para) {
          case 1:
            this.ControlCity = !this.ControlCity;
            break;
          case 2:
            this.ControlZone = !this.ControlZone;
            break;
          case 3:
            this.ControlHospital = !this.ControlHospital;
            break;
          case 4:
            this.ControlDepartment = !this.ControlDepartment;
        }
      },
      //疾病展开控制
      More(para) {
        switch (para) {
          case 1:
            this.ControlType1 = !this.ControlType1;
            break;
          case 2:
            this.ControlName1 = !this.ControlName1;
            break;
          case 3:
            this.ControlType2 = !this.ControlType2;
            break;
          case 4:
            this.ControlName2 = !this.ControlName2;
        }
      },

