      //改变当前阶段
      changeCurrentStage(para) {
        // if(this.stages.indexOf(para))
        this.currentstage = para + 1;
        // else
        //   this.currentstage=para;
        console.log(para)
      },
      //向上排序
      up(para) {
        if (para > 0) {
          //换内容
          let temp = this.stageContent[para];
          Vue.set(this.stageContent, para, this.stageContent[para - 1]);
          Vue.set(this.stageContent, para - 1, temp);

          let temp2 = this.stages[para];
          // this.stages[para].name=this.stages[para-1].name;
          // this.stages[para-1].name=temp2;
          Vue.set(this.stages, para, this.stages[para - 1]);
          Vue.set(this.stages, para - 1, temp2);
        }
        // console.log(this.stages)
      },
      //向下排序
      down(para) {
        // index不能动
        //换内容
        if (para < this.stageContent.length - 1) {
          let temp = this.stageContent[para];
          Vue.set(this.stageContent, para, this.stageContent[para + 1]);
          Vue.set(this.stageContent, para + 1, temp);
          //换名字
          let temp2 = this.stages[para];
          // this.stages[para].name=this.stages[para+1].name;
          // this.stages[para+1].name=temp2;
          Vue.set(this.stages, para, this.stages[para + 1]);
          Vue.set(this.stages, para + 1, temp2);
        }
      },
      //删除阶段
      DeleteStage(index) {
        if (this.stages.length > 1) {
          // console.log(index)
          if (index == this.stages.length - 1)
            this.itemstage--;
          // this.stages.pop();//为阶段顺序
          this.stages.splice(index, 1)
          // for(let i=index;i<=this.stages.length-1;i++){
          //   this.stages[i].index--;
          // }
          this.stageContent.splice(index, 1)
// console.log(index)
          console.log(this.currentstage)
          if (this.currentstage == index + 1) {
            // console.log(index)
            if (index == 0)
              this.currentstage = 1;
            else
              this.currentstage = index;
          }

          // console.log(this.currentstage)
          // console.log(this.stages)
          // console.log(this.stageContent)
        }
      },
      //重命名
      edit(index) {
        this.stages[index].editing = true
        // var _this=this;
        this.$nextTick(() => {
          // Vue.els.input[.focus()
          this.$refs.input[0].focus()
          // document.getElementById('input').focus()
        })
      },
      save(index) {
        this.stages[index].editing = false;
        this.stageContent[index].name = this.stages[index].name;
        // console.log(this.stages)
        // console.log(this.stageContent)
      },

