<template>
  <div>
    <div id="content">
      <button  v-if="!identified && isSuspended==0" data-toggle="modal" data-target="#confirm" class="btn btn-success">通知用户进行医疗损害鉴定</button>
      <!--<button   data-toggle="modal" data-target="#confirm" class="btn btn-success">通知用户进行医疗损害鉴定</button>-->
      <!--<label class="label1" >{{text}}</label>-->
      <label class="label1" v-else>{{text}}</label>
      <br>
      <label class="label1">鉴定结果</label> （用户在用户中心上传？）
      <textarea  v-model="currentStageContent.resultOfIdentify.text">{{currentStageContent.resultOfIdentify.text}}</textarea>

      <form @submit="submit" >
        <div class="field">
          <label class="label1">文件上传</label>
          <input name="file" type="file" multiple @change="getFile($event)">
        </div>
      </form>

      <div>
        <label class="label1">已上传文件</label>
        <br>
        <label v-for="item in currentStageContent.resultOfIdentify.files" style="">
          <a href="#">{{item.url}}</a>
        </label>
      </div>
    </div>

    <div class="foot">
      <button class="btn btn-primary" @click="submit" >提交</button>
      <button class="btn btn-primary" @click="next" >跳过</button>
      <button class="btn btn-primary" @click="next" >下一步</button>
    </div>

    <!-- 通知确认框 -->
    <div class="modal fade" id="confirm" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">确认框</h4>
          </div>
          <div class="modal-body">是否确认通知？</div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" @click="InformIndenty">确定</button>
            <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "identification",
    props: ['currentStageContent',"caseId",'identified',"isSuspended"],
    data() {
      return {
        text:'已通知用户进行医疗损害鉴定',
        fileFlag:false,
      }
    },
    methods: {
      next() {
        this.$emit('next', 0)
      },
      submit(){
        event.preventDefault();//取消默认行为
        //创建 formData 对象
        let formData = new FormData();
        // 向 formData 对象中添加文件
        if(this.file!=''){
          formData.append('files',this.file);
        }
        formData.append('caseId',this.caseId)
        formData.append('text',this.currentStageContent.resultOfIdentify.text)
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/mediator/resultOfIndent',
          data:formData
        })
        alert('已提交');
        this.$emit('next', 0)
      },
      InformIndenty(){
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/mediator/InformIndenty',
          data:{
            caseId:this.caseId
          }
        })
        this.isSuspended=1;
        $("#confirm").modal("hide");
      },
      getFile(event) {
        this.file = event.target.files[0];
        console.log(this.file);
      },
    },
    mounted(){
      // if(this.currentStageContent.resultOfIdentify.files!=undefined)
      //   if(this.currentStageContent.resultOfIdentify.files.length>=0)
      //     this.fileFlag=true;
    }
  }
</script>

<style scoped>
  .label1{
    font-size: 20px;
    color: #3a8dbe;
    margin-top: 10px;
  }
  button {
    font-size: 16px;
    border-radius: 5px;
  }

  #content textarea {
    height:300px;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 10px;
    border-radius: 10px;
    border: none;
    background-color:rgba(0,0,0,0.05);
    overflow: scroll;
    overflow-x: hidden;
  }
  .foot {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    text-align: center;
    position: absolute;
    bottom: 15px;
  }
  .foot button{
    margin-left: 20px;
    margin-right: 20px;
    width: 80px;
  }
</style>



