<template>
  <div>
    <div class="info">
      <div class="personInfo">
        <label class="label1">涉事人员信息</label>
        <div class="tableInfo">
          <table>
            <tr v-for="(item,index) in InvolvedPeople">
              <td>
                <input type="radio"  v-model="item.picked" value="申请人" >
                <label class="label2" >申请人</label>
              </td>
              <td>
                <input type="radio"  v-model="item.picked" value="被申请人">
                <label class="label2">被申请人</label>
              </td>
              <td>
                <input type="radio" :id="index" v-model ="item.picked" value="代理人" @change="handle(index)">
                <label class="label2">代理人</label>
              </td>
              <td>
                <label class="label2">姓名</label>
                <input type="text" v-model="item.name">
              </td>
              <td>
                <label class="label2">身份证号</label>
                <input type="text" v-model="item.cardID">
              </td>
              <td>
                <label class="label2">手机号码</label>
                <input type="text" v-model="item.phone">
              </td>
              <td>
                <button class="btn btn-primary btn-sm" @click="DeletePerson(index)">x</button>
              </td>
            </tr>
          </table>
        </div>
        <div class="ButtonID">
          <button class="btn btn-primary" @click="AddPerson()">添加</button>
        </div>
      </div>

      <label class="label1">历史案例</label>
      <div class="show">
        <table class="table   table-hover" v-if="role=='调解员'" >
          <tbody>
          <tr v-for="item in history" style="border: none">
            <td>{{item}}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="confirm">
      <button class="btn btn-primary" @click="NextStep">下一步</button>
    </div>
  </div>

</template>

<script>
  import Vue from 'vue'
  export default {
    name: "InvolvedInfo",
    data() {
      return {
        phone:sessionStorage.getItem("phone"),
        role:sessionStorage.getItem("role"),
        id:sessionStorage.getItem("id"),
        caseId:sessionStorage.getItem("CaseId"),
        items: [1, 2, 3],
        ClickNum: 4,
        NumOfSub:0,
        InvolvedPeople: [
          {
            name: "",
            cardID: "",
            phone:"",
            picked: ''
          },
          {
            name: "",
            cardID: "",
            phone:"",
            picked: ''
          },
          {
            name: "",
            cardID: "",
            phone:"",
            picked: ''
          }
        ],
        history:[],
        ResultInvolvedPeole:[],
      }
    },
    methods: {
      handle(para){
        console.log("para"+para);
        for(var i=0;i<this.InvolvedPeople.length;i++){
          if(this.InvolvedPeople[i].picked==="代理人"){
            this.NumOfSub++;
          }
          if(this.NumOfSub>=4){
            alert("代理人至多三人");
            Vue.set(this.InvolvedPeople,para,{
              name: "",
              cardID: "",
              phone:"",
              picked: ""
            });
            break;
          }
        }
        this.NumOfSub=0;
      },
      AddPerson() {
        let temp = {
          name: '',
          cardID: '',
          picked: ''
        };
        this.InvolvedPeople.push(temp)
      },
      DeletePerson(index) {
        this.InvolvedPeople.splice(index,1)
      },
      NextStep() {
        this.ResultInvolvedPeole=this.InvolvedPeople.filter((item)=>{
          return item.phone!==''
        })
        this.$router.push({path: 'BasicDivideInfo'});
        this.$axios({
          method: 'post',
          url: 'https://www.wangj1106.top/RegConflict/InvolvedPeopleInfo',
          data: {
            InvolvedPeople:this.ResultInvolvedPeole,
            id:this.id,
          }
        })
          .then((res)=>{
            this.caseId=res.data.data.CaseId;
            sessionStorage.setItem("caseId",this.caseId)
          })
          .catch((error) => {
            console.log(error)
          })
      },
    },
  }
</script>

<style scoped>
  .info {
    width: 100%;
  }
  .tableInfo{
    height:220px;
    overflow: scroll;
    overflow-x:hidden;
    padding: 10px 20px;
    border-radius: 10px;
    background-color:rgba(0,0,0,0.03);
  }
  .show{
    height:300px;
    overflow: scroll;
    overflow-x:hidden;
    padding: 10px 20px;
    border-radius: 10px;
    background-color:rgba(0,0,0,0.03);
  }

  table{
    width: 100%;
    border-collapse:   separate;
    border-spacing:   20px;
  }

  .label1 {
    margin: 8px auto;
    font-size: 20px;
    color: #3a8dbe;
  }

  .label2{
    font-weight: lighter;
  }

  input[type=text] {
    width: 180px;
    background: none;
    border-radius: 10px;
  }

  select {
    background-color: transparent;
    border-radius: 10px;
    width: 200px;
    margin-left: 20px;
  }

  .ButtonID{
    width: 100%;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .ButtonID button {
    width: 80px;
  }
  .confirm {
    width: 100%;
    text-align: center;
    margin-top: 20px;
  }
  button {
    font-size: 16px;
    border-radius: 5px;
  }
</style>



