        <button class="btn btn-success" @click="onPersonconsult" style="float: right"><strong>转人工咨询</strong></button>
      </div>
      <div class="col-lg-4 col-md-4 col-sm-4 col-xs-4"  style="text-align: center">
        <label class="label1">法律法规</label>
        <label class="label1">|</label>
        <label class="label1">处置流程</label>
        <label class="label1">|</label>
