        </div>
      </div>
      <div class="col-lg-4 col-md-4 col-sm-4 col-xs-4">
        <div class="show">
             <textarea rows="31" disabled placeholder="法律法规"></textarea>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  import LoginForm from '../../components/loginForm'
  import RegisterForm from '../../components/registerForm'
  export default {
    name: "consult",
    components: {
      LoginForm,RegisterForm
    },
    data(){
      return{
        LoginIsShow:true,
      }
    },
    mounted(){
      this.role=sessionStorage.getItem("role");
      this.phone=sessionStorage.getItem("phone");
      this.id=sessionStorage.getItem("id")
      if(this.phone==undefined)
        this.LoginIsShow=true;
      else if(this.role=='普通用户'||this.role=='院方人员')
        this.LoginIsShow=false;
      else
        this.LoginIsShow=true;
    },
    methods: {
      onPersonconsult() {
        this.$router.push({path: './personConsult'})
        // window.open('http://127.0.0.1:8182/webdemo/im/main.html',"_blank")
      },
      login(phone, data) {
        this.phone = name;
        this.role = data.role;
        this.id=data.id;
        sessionStorage.setItem("phone", this.phone)
        sessionStorage.setItem("role", this.role)
        sessionStorage.setItem("id", this.id)
      },
      logout(){
        sessionStorage.clear();
        location.reload();
      }
    }
  }
</script>


<style scoped>
  .container {
    margin-top: 40px;
    width: 95%;
    padding:10px 10px;
    margin-left: auto;
    margin-right: auto;
  }

  .label1{
    font-size: 30px;
    color: #2e6da4;
  }

  .show {
    border:2px solid rgba(195, 199, 193, 0.49);
    text-align: center;
    border-radius: 10px;
    padding: 15px 15px;

  }
  button {
    font-size: 16px;
    outline: none;
    border: none;
    border-radius: 5px;
  }
  textarea{
    border: none;
    background: rgba(227, 228, 232, 0.54);
    width: 100%;
    border-radius: 5px;
    overflow: scroll;
    overflow-x: hidden;
  }
</style>



