<template>
  <div>
    <div>
      <ul class="nav nav-pills navbar-right">
      <li>
      <a data-toggle="modal" data-target="#login" href="">
        <span class="glyphicon glyphicon-log-in"></span>
        <label>登录</label>
      </a>
      </li>
      </ul>
    </div>
    <!-- 登录窗口 -->
    <div id="login" class="modal fade">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-body">
            <button class="close" data-dismiss="modal">
              <span>&times;</span>
            </button>
          </div>
          <div class="modal-title">
            <h1 class="text-center">登录</h1>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <div class="form-group">
                <label for="">用户名</label>
                <span class="form-error">{{ userErrors.errorText }}</span>
                <input class="form-control" type="text" placeholder="11位手机号" v-model="phone">
              </div>
              <div class="form-group">
                <label for="">密码</label>
                <span class="form-error">{{ passwordErrors.errorText }}</span>
                <input class="form-control" type="password" placeholder="6-16位字母或数字" v-model="password">
              </div>
              <div class="text-center">
                <button class="btn btn-success" type="submit" @click="login">登录</button>
                <button class="btn btn-primary" data-dismiss="modal">取消</button>
                <br>
                <a href="" data-toggle="modal" data-dismiss="modal" data-target="#register">还没有账号？点我注册</a>
              </div>
              <p style="color: red">{{ errorText }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        phone: '',
        password: '',
        errorText: '',
        id:'',
        role:'',
      }
    },
    computed: {
      userErrors() {
        let errorText, status
        if (!/^\d{11}$/g.test(this.phone)) {
          status = false
          errorText = '请输入正确的手机号'
        }
        else {
          status = true
          errorText = ''
        }
        if (!this.userFlag) {
          errorText = ''
          this.userFlag = true
        }
        return {
          status,
          errorText
        }
      },
      passwordErrors() {
        let errorText, status
        if (!/^\w{6,16}$/g.test(this.password)) {
          status = false
          errorText = '密码不是6-16位'
        }
        else {
          status = true
          errorText = ''
        }
        if (!this.passwordFlag) {
          errorText = ''
          this.passwordFlag = true
        }
        return {
          status,
          errorText
        }
      }
    },
    methods:{
      login() {
        if (!this.userErrors.status || !this.passwordErrors.status) {
          this.errorText = '部分选项未通过'
        }
        else {
          console.log(this.phone)
          this.$axios.post('https://www.wangj1106.top/user/login',{phone: this.phone, password: this.password})
            .then((res) => {
              if (res.data.code == 0) {
                this.role = res.data.data.role;
                this.id=res.data.data.id;
                this.$emit('login',this.phone,res.data);
                location.reload();
              }
              this.errorText = res.data.msg;

            })
            .catch((error) => {
              console.log(error)
            })
        }


      },
    }
  }
</script>

<style scoped>
  #login{
    font-size: 16px;
  }
  .form-error {
    color: red;
    padding-left: 15px;
  }
  a{
    color: black;
    cursor: pointer;
  }
  label{
    font-size: 16px;
    /*font-weight: lighter;*/
  }
  input{
    font-size: 16px;
  }
</style>



