<template>
  <div>
    <div class="info">
      <div>
        <label>调解员主观评判案件</label>
        <textarea class="detail" placeholder="调解员主观评判案件" v-model="evaluateOfCase"></textarea>
      </div>
      <div>
        <label>调解员对用户画像</label>
        <textarea class="detail" placeholder="调解员对用户画像" v-model="evaluateOfUser"></textarea>
      </div>
      <div>
        <label>调解员提出调解意见</label>
        <textarea  class="detail" placeholder="调解员提出调解意见" v-model="evaluateOfMediation"></textarea>
      </div>
    </div>
    <div class="finish">
      <button class="btn btn-primary" @click="ok()">完成</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "ModeratorRegister",
    data() {
      return {
        evaluateOfCase:'',
        evaluateOfUser:'',
        evaluateOfMediation:'',
      }
    },
    methods: {
      ok() {
        this.$axios({
          methos: 'post',
          url: 'https://www.wangj1106.top/DisputeWeb/Register/postEvaluateOfMediator',
          data: {evaluateOfCase: this.evaluateOfCase,evaluateOfUser:this.evaluateOfUser,evaluateOfMediation:this.evaluateOfMediation}
        })
        this.$alert('已提交，登记已完成', '', {
          confirmButtonText: '确定',
        })
      }
    }
  }
</script>

<style scoped>
  .info {
    width: 100%;
  }
  .detail{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    height:180px;
    overflow: scroll;
    overflow-x:hidden;
    padding: 10px 20px;
    border-radius: 10px;
    background-color:rgba(0,0,0,0.03);
  }
  label{
    margin: 8px auto;
    font-size: 20px;
    color: #3a8dbe;
  }
  .finish{
    text-align: center;
    margin-top: 20px;
  }

  button{
    font-size: 16px;
    outline: none;
    border: none;
    border-radius: 5px;
    width: 80px;
  }

</style>



