      //   // link.click()
      // },

      changeInstitute(para){
        this.currentInsititue=para;
        console.log(this.currentInsititue)
      },
      inquireInstitute(para){
        this.tempCaseId=para.caseId;
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/getHospitalMessage',
          data:{
            caseId:this.tempCaseId,
          }
        })
          .then((res)=>{
            this.instituteMessage=res.data.data;
            this.inquireMessage.splice(0,1);
            for(let i=0;i<this.instituteMessage.length;i++) {
              let temp =
                {
                  hospital: this.instituteMessage[i].name,
                  inquireDate: '',
                  inquireReplyPerson: '',
                  inquireResult: '',
                  otherMessage: '',
                };
              this.inquireMessage.push(temp);
            }
          })
          .catch((error) => {
            console.log(error)
          })
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/getInqueryHospitalList',
          data:{
            caseId:this.tempCaseId,
          }
        })
          .then((res)=>{
            this.inquireHistory=res.data.data
            if(this.inquireHistory!=undefined){
              for(let i=0;i<this.inquireHistory.length;i++) {
                if(this.inquireHistory[i].inquireResult == "1")
                  this.inquireHistory[i].inquireResult = '接受调解';
                else
                  this.inquireHistory[i].inquireResult = '拒绝调解';
              }
            }
            this.inquireShow=true
          })
          .catch((error) => {
            console.log(error)
          })
      },
      closeInquire(){
        this.inquireShow=false;
      },
      submitInquire(){
        this.$axios({
          method:'post',
          url:'https://www.wangj1106.top/DisputeWeb/inqueryHospital',
          data:{
            isFinished:this.isFinished,
            caseId:this.tempCaseId,
            mediatorId:sessionStorage.getItem("id"),
            inquireMessage:this.inquireMessage,
          }
        })
          .then((res) => {
            let nextStatus=0;
            if(this.isFinished==1)
              nextStatus=9;
            if(this.isFinished==2)
              nextStatus=8;
            if(res.data.code>=0)
            {
              let index = this.data.findIndex(item => {
                return item.caseId == this.tempCaseId
              })
              Vue.set(this.data, index, {
                date: this.data[index].date,
                name: this.data[index].name,
                caseId: this.data[index].caseId,
                status: nextStatus,
                applicant: this.data[index].applicant,
                respondent: this.data[index].respondent,
              })
            }
            this.inquireShow=false;
          })
          .catch((error) => {
            console.log(error)
          });
      },
      downLoadFile(para){
        // window.open(para,'_blank')
        window.location.href=para;
      },
    },
  }

</script>

<style scoped>
  #content {
    padding-left: 8px;
    padding-right: 8px;
  }
  .table{
    width: 100%;
    margin-top: 10px;
  }
  .tablehead{
    font-size: 18px;
  }
  .tablebody{
    font-size: 16px;
  }
  button{
    border-radius: 5px;
  }

  .detail{
    width: 100%;
    border-radius: 5px;
    border-style: ridge;
    background-color:rgba(0,0,0,0.02);
  }
  .label1{
    margin-top: 5px;
    font-size: 16px;
    color: #3a8dbe;
  }
  a{
    cursor: pointer;
  }
  .inquireContent{
    padding: 10px;
    border-left:1px solid rgba(195, 199, 193, 0.84);
    border-right:1px solid rgba(195, 199, 193, 0.84);
    border-bottom:1px solid rgba(195, 199, 193, 0.84);
  }
</style>



